// NOTE(dbildstr): needed for browsers that do not support Promise (i.e IE11)
import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { exposeDevCode } from 'core/devmode';
import { initializeWindow } from '@webui-one/core/utils/browser';
import CometContainer from 'modules/comet/CometContainer';
import { initializeTransactions } from 'modules/transactions/utils';
import {
    Route,
    Router,
    Switch,
    browserHistory,
    syncHistoryWithStore,
} from '@webui-one/router';
import AlarmManagerRoutes from 'AlarmManager';
import ApplicationHubRoutes from 'ApplicationHub';
import CommitManagerRoutes from 'CommitManager';
import ConfigurationEditorRoutes from 'ConfigurationEditor';
import DashboardRoutes from 'Dashboard';
import HighAvailabilityRoutes from 'HighAvailability';
import DeviceManagerRoutes from 'DeviceManager';
import PackageUpgradeRoutes from 'PackageUpgrade';
import ServiceManagerRoutes from 'ServiceManager';
import ErrorRoute from './ErrorPage/ErrorRoute';
import ApplicationContainer from './ApplicationContainer';
import { configureStore } from './store/configureStore';

initializeWindow();

const store = configureStore();
const history = syncHistoryWithStore(browserHistory, store);

initializeTransactions(store);

exposeDevCode(store);

ReactDOM.render(
    <Provider store={store}>
        <div>
            <CometContainer
                upgrade
                messages
            />
            <Router history={history}>
                <ApplicationContainer>
                    <Switch>
                        <Route {...AlarmManagerRoutes} />
                        <Route {...ApplicationHubRoutes} />
                        <Route {...CommitManagerRoutes} />
                        <Route {...ConfigurationEditorRoutes} />
                        <Route {...DashboardRoutes} />
                        <Route {...HighAvailabilityRoutes} />
                        <Route {...DeviceManagerRoutes} />
                        <Route {...PackageUpgradeRoutes} />
                        <Route {...ServiceManagerRoutes} />
                        <Route {...ErrorRoute} />
                    </Switch>
                </ApplicationContainer>
            </Router>
        </div>
    </Provider>,
    document.getElementById('root'),
);
