import * as actionTypes from './actionTypes';
import { LOCATION_CHANGE } from '@webui-one/router';
import { getApplicationKey } from '@webui-one/core/utils/path';

const APP_LAST_LOCATION = {
    CommitManager: true,
    ConfigurationEditor: true,
    ServiceManager: true,
};

const initialState = {
    authenticated: true,
    lastLocation: {},
    lastLoginDisplayed: false,
    shortcutPanelIsVisible: true,
};

const shouldUpdateLastLocation = (appKey, lastLocation, action) =>
    !!APP_LAST_LOCATION[appKey]
        && (!lastLocation[appKey] || lastLocation[appKey] !== action.location.pathname);


const reducer = (state = initialState, action = {}) => {
    switch (action.type) {
    case actionTypes.APPLICATION_NAME_SET:
        return { ...state, name: action.name };
    case actionTypes.SHOW_HELP:
        return { ...state, helpIsVisible: action.value };
    case actionTypes.SHOW_SETTINGS:
        return { ...state, settingsIsVisible: action.value };
    case actionTypes.SHOW_SHORTCUT_PANEL:
        return { ...state, shortcutPanelIsVisible: action.value };
    case actionTypes.AUTHENTICATED_SET:
        return { ...state, authenticated: action.authenticated };
    case actionTypes.LAST_LOGIN_DISPLAYED:
        return { ...state, lastLoginDisplayed: action.value };
    case LOCATION_CHANGE: {
        const appKey = getApplicationKey(action.location.pathname);
        const lastLocation = state.lastLocation;
        if (shouldUpdateLastLocation(appKey, lastLocation, action)) {
            return {
                ...state,
                lastLocation: {
                    ...state.lastLocation,
                    [appKey]: action.location.pathname,
                },
            };
        }
        return state;
    }
    default:
        return state;
    }
};

export default reducer;
