import * as actionTypes from 'modules/nodes/actionTypes';
import * as dataActionTypes from 'modules/data/actionTypes';
import * as transactionActionTypes from 'modules/transactions/actionTypes';
import * as yangActionsActionTypes from 'modules/yangActions/actionTypes';
import { REEVALUATE_SCHEMA } from 'modules/schemas/actionTypes';
import {
    findCommonAncestor,
    pathWithoutKeys,
    pathToObjects,
} from '@webui-one/core/keypath/kp-parser';

const initialState = {
};

export const loadStatus = {
    LOADING: 'LOADING',
    ERROR: 'ERROR',
    COMPLETE: 'COMPLETE',
};

const isActionChild = (state, path) => (
    Object.keys(state).reduce((acc, cur) => {
        const val = state[cur];
        if (path.startsWith(cur) && val.isAction === true) return true;
        return acc;
    }, false)
);

export const selectNodes = state => state.nodes;

const presenceConfig = (presenceData) => {
    const config = { presence: true };
    if (presenceData && presenceData.exists) {
        config.exists = true;
    }
    return config;
};

const getRows = (state, path) => (state[path] || {}).rows || [];

const omitNodes = (state, path) => Object.keys(state).reduce((acc, n) => {
    if (n !== path && !n.startsWith(`${path}/`)) {
        acc[n] = state[n];
    } else if (state[n].presence) {
        acc[n] = presenceConfig();
    }
    return acc;
}, {});

const omitRowsInParentList = (state, path) => Object.keys(state).reduce((acc, nodePath) => {
    const { name, parentPath } = pathToObjects(path).pop();
    if (nodePath === parentPath) {
        const newRows = state[nodePath].rows.filter(row => row.name !== name);
        acc[nodePath] = {
            ...state[nodePath],
            rows: newRows,
        };
    } else {
        acc[nodePath] = state[nodePath];
    }
    return acc;
}, state);

const handleDelete = (state, action) => ({
    ...omitNodes(state, action.path),
});

const handleDeletePaths = (state, action) => (
    action.paths.reduce((acc, path) => ({
        ...omitRowsInParentList(omitNodes(state, path), path),
    }), state)
);

const handleDeleteNodeAndChildren = (state, action) => ({
    ...omitNodes(state, action.path),
});

const reduceHadData = (node = {}, action = {}) => {
    const hadData = node.hadData || {};
    const rows = action.result.results;
    if (rows.length < 1) return hadData;
    const keys = Object.keys(rows[0]);
    return keys.reduce((acc, key) => {
        if (acc[key]) return acc;
        return {
            ...acc,
            [key]: !rows.every(row => row[key] === undefined),
        };
    }, hadData);
};

const getKeypathFromCommonAncestor = (keypath, schemaPath) => {
    const commonAncestor = findCommonAncestor(keypath, schemaPath);
    if (!commonAncestor) return undefined;
    return schemaPath.replace(pathWithoutKeys(commonAncestor), commonAncestor);
};

const handleReEvaluateSchemaAction = (state, action) => {
    const { path, dependants } = action;
    if (!dependants || dependants.length === 0) return state;

    const reLoadPaths = dependants.map(p => getKeypathFromCommonAncestor(path, p));

    const updatedState = reLoadPaths.reduce((acc, cur) => {
        if (cur === path) return acc;
        acc[cur] = {
            ...state[cur],
            reloadValues: true,
        };
        return acc;
    }, {});
    return {
        ...state,
        ...updatedState,
    };
};

const handleModifiedNodes = (state, action) => {
    const newState = { ...state };
    action.paths.reduce((acc, path) => {
        const node = {
            ...acc[path],
            isModified: true,
        };
        acc[path] = node;
        return acc;
    }, newState);
    return newState;
};

const dataIsLeafList = data => (
    Array.isArray(data.rows) &&
    data.rows.length > 0 &&
    data.rows.length === data.totalNumberOfResults &&
    data.rows.every(r => Object.keys(r).length === 1 && r[0] !== undefined)
);

const handleDataComplete = (state, action) => {
    const newNodes = Object.entries(action.data).reduce((acc, [keypath, data]) => {
        if (dataIsLeafList(data)) {
            acc[keypath] = { ...data };
        }
        return acc;
    }, {});
    if (Object.keys(newNodes).length === 0) {
        return state;
    }
    return {
        ...state,
        ...newNodes,
    };
};

export default function nodes(state = initialState, action = {}) {
    switch (action.type) {
    case actionTypes.MODIFIED_NODES:
        return handleModifiedNodes(state, action);
    case actionTypes.CHANGE_VALUE:
    case actionTypes.SET_LOCAL_VALUE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                newValue: action.value,
                error: undefined,
            },
        };
    case actionTypes.CREATE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                value: action.value,
                exists: true,
                error: undefined,
                default: undefined,
            },
        };
    case actionTypes.DELETE:
        return handleDelete(state, action);
    case actionTypes.DELETE_PATHS:
        return handleDeletePaths(state, action);
    case actionTypes.DELETE_NODE_AND_CHILDREN:
        return handleDeleteNodeAndChildren(state, action);
    case actionTypes.CURRENT_ROOT_NODE:
        return { ...state, currentRootPath: action.path };
    case actionTypes.EXISTS_PENDING:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                existsPending: true,
            },
        };
    case actionTypes.EXISTS_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                existsPending: false,
            },
        };
    case actionTypes.EXISTS:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                exists: action.exists,
                existsPending: false,
            },
        };
    case actionTypes.GET_LEAFREF_VALUES_START:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                error: undefined,
                values: undefined,
                isLoading: true,
                reloadValues: undefined,
            },
        };
    case actionTypes.GET_LEAFREF_VALUES_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                values: undefined,
                error: action.error,
                isLoading: false,
            },
        };
    case actionTypes.GET_LEAFREF_VALUES_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                values: action.values,
                sourcePath: action.sourcePath,
                error: undefined,
                isLoading: false,
            },
        };
    case actionTypes.GET_VALUE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                value: action.value,
                isLoading: false,
                error: undefined,
            },
        };
    case actionTypes.GET_VALUE_ERROR:
    case actionTypes.SET_VALUE_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                isLoading: false,
                error: action.error,
            },
        };
    case actionTypes.GET_VALUE_PENDING:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                isLoading: true,
            },
        };
    case actionTypes.HIDE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                isHidden: action.value,
            },
        };
    case actionTypes.LOADING:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                loaded: false,
                loading: true,
            },
        };
    case actionTypes.LOADING_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                loaded: true,
                loading: false,
            },
        };
    case actionTypes.SET_VALUE_START:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                isLoading: true,
                error: undefined,
            },
        };
    case actionTypes.SET_VALUE_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                newValue: action.value,
                isLoading: false,
                error: undefined,
            },
        };
    case actionTypes.SET_VALUE_INTERRUPT:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                isLoading: false,
            },
        };
    case actionTypes.SET_CHECKBOX_VALUE:
        return {
            ...state,
            [action.path]: {
                ...state[action.path],
                selectedRows: action.keyPaths.reduce((acc, keyPath) => ({
                    ...acc,
                    [keyPath]: action.value,
                }), state[action.path].selectedRows),
            },
        };
    case transactionActionTypes.COMMIT_COMPLETE:
    case transactionActionTypes.DELETE_TRANS:
        return initialState;
    case actionTypes.QUERY:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                hadData: reduceHadData(state[action.path], action),
                error: undefined,
                isLoading: false,
                isModified: false,
                offset: action.result.current_position,
                rows: action.result.results,
                sort: action.sort,
                sortOrder: action.sortOrder,
                totalNumberOfResults: action.result.total_number_of_results,
            },
        };
    case actionTypes.DELETE_START:
    case actionTypes.QUERY_PENDING:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                error: undefined,
                isLoading: true,
            },
        };
    case actionTypes.QUERY_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                error: action.error,
                isLoading: false,
            },
        };
    case actionTypes.DELETE_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                error: undefined,
                isLoading: false,
            },
        };
    case actionTypes.GET_CASE_START:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                loadStatus: loadStatus.LOADING,
            },
        };
    case actionTypes.GET_CASE_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                caseValue: action.caseValue,
                loadStatus: loadStatus.COMPLETE,
            },
        };
    case actionTypes.GET_CASE_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                loadStatus: loadStatus.ERROR,
                error: action.error,
            },
        };
    case actionTypes.SET_CASE_COMPLETE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                caseValue: action.value,
                loadStatus: loadStatus.COMPLETE,
            },
        };
    case actionTypes.SET_CASE_ERROR:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                caseValue: undefined,
                loadStatus: loadStatus.ERROR,
            },
        };
    case actionTypes.SET_CASE_START:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                caseValue: undefined,
                loadStatus: loadStatus.LOADING,
            },
        };
    case actionTypes.CLEAR_CASE:
        return { ...state,
            [action.path]: {
                ...state[action.path],
                caseValue: undefined,
                loadStatus: undefined,
            },
        };
    case REEVALUATE_SCHEMA: {
        return handleReEvaluateSchemaAction(state, action);
    }
    case dataActionTypes.GET_DATA_COMPLETE: {
        return handleDataComplete(state, action);
    }
    case yangActionsActionTypes.LOAD_ACTION_ACTION: {
        return { ...state,
            [action.actionPath]: {
                ...state[action.actionPath],
                isAction: true,
            },
        };
    }
    default:
        return state;
    }
}

export {
    getRows as _getRows,
    handleDataComplete as _handleDataComplete,
    handleModifiedNodes as _handleModifiedNodes,
    handleReEvaluateSchemaAction as _handleReEvaluateSchemaAction,
    isActionChild as _isActionChild,
    omitNodes as _omitNodes,
};
