import { createScanner } from '../utils/parsing';
import { isString } from '../utils/string';

export const invalidKeyCharError = char =>
    `Expected valid key character or end of input but found "${char}".`;

const KEY_LC = '{';
const KEY_RC = '}';
const NAME_SEP = '/';
const NAME = 'name';
const NS = 'ns';
const NS_SEP = ':';
export const isIdentifierStartChar = char => (char >= 'a' && char <= 'z')
    || (char >= 'A' && char <= 'Z')
    || char === '_';

export const isIdentifierChar = char => isIdentifierStartChar(char)
    || (char >= '0' && char <= '9')
    || char === '-'
    || char === '.';

// %x9 / %xA / %xD / %x20-D7FF / %xE000-FFFD / %x10000-10FFFF
export const isValidKeyCode = code => (
    code === 0x09
    || code === 0x0A
    || code === 0x0D
    || (code >= 0x20 && code <= 0xD7FF)
    || (code >= 0xE000 && code <= 0xFFFD)
    || (code >= 0x10000 && code <= 0x10FFFF)
);
export const isValidKeyChar = char => isValidKeyCode(char.codePointAt(0));

export function parseNameSeparator(scanner) {
    scanner.emit(NAME_SEP);
    const char = scanner.next();
    if (scanner.isEOF()) {
        return null;
    }
    if (!isIdentifierStartChar(char)) {
        scanner.error(
            'SyntaxError',
            `Expected [a-zA-Z_] or end of input but found "${char}".`,
        );
        return null;
    }
    return parseIdentifier; // eslint-disable-line no-use-before-define
}

export function parseKeyEnd(scanner) {
    if (!scanner.hasOption('ignoreListEntries', true)) {
        scanner.emit(KEY_RC);
    }
    const char = scanner.next();
    if (char === '/') {
        return parseNameSeparator;
    }
    if (!scanner.isEOF()) {
        scanner.error('SyntaxError', `Expected "/" but found "${char}".`);
    }
    return null;
}

function ending(state) {
    if (state.error || state.endOfKey) {
        return state;
    }
    if (!state.scanner.hasOption('ignoreListKeys', true)
        && !state.scanner.hasOption('ignoreListEntries', true)) {
        // Include the parsed key without quotes and escapes in the emitted token
        state.scanner.emit('key', { key: state.key });
    }

    // Return next state or null if end of input

    if (state.scanner.isEOF()) {
        state.scanner.error(
            'SyntaxError',
            'Expected a key or "}" but found end of input.',
        );
        return {
            ...state,
            error: true,
        };
    }
    if (state.scanner.next() === '}') {
        // move start offset to current char for the next state function
        // if we didn't emit anything earlier
        state.scanner.ignore();
        return {
            ...state,
            endOfKey: true,
        };
    }
    return state;
}

const takeChar = (prevState) => {
    const state = prevState;
    if (!state.escaped
        && !state.quoteChar
        && (state.quoteChars.includes(state.char))
        && !state.initialQuotesOnly
    ) {
        state.quoteChar = state.char;
    } else {
        state.key += state.char;
    }
    return state;
};
const isEndOfKey = (prevState) => {
    const state = prevState;
    if (!state.escaped
        && !state.quoteChar
        && (state.char === '}' || state.char === ' ')
    ) {
        state.exit = true;
    }
    return state;
};

const checkCharValidity = (prevState) => {
    const state = prevState;
    if (!isValidKeyChar(state.char)) {
        state.scanner.error('SyntaxError', invalidKeyCharError(state.char));
        state.error = true;
    }
    return state;
};
const isEndOfQuote = (prevState) => {
    const state = prevState;
    if (!state.char
        || (!state.escaped
            && state.quoteChar
            && state.quoteChar === state.char
        )
    ) {
        state.scanner.next();
        state.exit = true;
    }
    return state;
};
const isEscapeChar = (prevState) => {
    const state = prevState;
    if (!state.escaped && state.char === '\\') {
        state.scanner.next();
        state.escaped = true;
        state.char = state.scanner.peek();
    }
    return state;
};
const peekChar = (prevState) => {
    const state = prevState;
    state.char = state.scanner.peek();
    return state;
};

const nextChar = (prevState) => {
    const state = prevState;
    state.scanner.next();
    state.escaped = false;
    return state;
};

function createKeyState(scanner) {
    let char = scanner.current();
    // skip leading whitespace
    while (char === ' ') {
        char = scanner.next();
    }
    // Do not include the spaces skipped above in the token
    scanner.ignore();

    const quoteChars = scanner.getOption('quoteChars', ['\'', '"']);
    const initialQuotesOnly = scanner.getOption('initialQuotesOnly', false);
    // Start parsing
    const quoteChar = quoteChars.includes(char) ? char : '';
    const escaped = char === '\\';
    const key = !quoteChar && !escaped ? char : '';

    const state = {
        exit: false,
        error: false,
        escaped,
        char,
        key,
        initialQuotesOnly,
        quoteChar,
        quoteChars,
        scanner,
    };
    // Return without further parsing if invalid or no key was found
    if (scanner.isEOF()) {
        scanner.error(
            'SyntaxError',
            'Expected a key or "}" but found end of input.',
        );
        state.error = true;
        return state;
    }
    if (char === '}') {
        state.endOfKey = true;
        return state;
    }
    if (!isValidKeyChar(char)) {
        scanner.error('SyntaxError', invalidKeyCharError(char));
        state.error = true;
        return state;
    }
    return state;
}

const keepScanning = state => (
    !state.scanner.isEOF()
    && !state.error
    && !state.exit
    && !state.endOfKey
);

const keyParsers = [
    peekChar,
    isEscapeChar,
    isEndOfQuote,
    checkCharValidity,
    isEndOfKey,
    takeChar,
    nextChar,
];

export function parseKey(scanner) {
    let state = createKeyState(scanner);

    let index = 0;

    while (keepScanning(state)) {
        const keyParser = keyParsers[index];
        state = keyParser(state);
        index = (index + 1) % keyParsers.length;
    }

    state = ending(state);

    if (state.error) {
        return null;
    }

    if (state.endOfKey) {
        return parseKeyEnd;
    }

    return parseKey;
}

export function parseKeyStart(scanner) {
    if (!scanner.hasOption('ignoreListEntries', true)) {
        scanner.emit(KEY_LC);
    }
    const char = scanner.next();
    if (scanner.isEOF()) {
        scanner.error(
            'SyntaxError',
            'Expected a key or "}" but found end of input.',
        );
        return null;
    }
    if (char === '}') {
        return parseKeyEnd;
    }
    return parseKey;
}

export function parseNamespaceSeparator(scanner) {
    scanner.emit(NS_SEP);
    const char = scanner.next();
    if (scanner.isEOF()) {
        scanner.error(
            'SyntaxError',
            'Expected [a-zA-Z_] but found end of input.',
        );
        return null;
    }
    if (!isIdentifierStartChar(char)) {
        scanner.error(
            'SyntaxError',
            `Expected [a-zA-Z_] but found "${char}".`,
        );
        return null;
    }
    return parseIdentifier; // eslint-disable-line no-use-before-define
}

export function parseIdentifier(scanner) {
    while (isIdentifierChar(scanner.peek())) {
        scanner.next();
    }
    const tokens = scanner.getTokens();
    const prevToken = tokens[tokens.length - 1];

    // Emit name or namespace token depending on the following character
    scanner.emit(scanner.peek() === ':' ? NS : NAME);
    const char = scanner.next();
    if (char === ':') {
        if (prevToken && prevToken.type === NS_SEP) {
            scanner.error(
                'SyntaxError',
                `Expected "/" or "{" but found "${char}".`,
            );
            return null;
        }
        return parseNamespaceSeparator;
    }
    if (char === '{') {
        return parseKeyStart;
    }
    if (char === '/') {
        return parseNameSeparator;
    }
    if (!scanner.isEOF()) {
        scanner.error(
            'SyntaxError',
            `Expected "/" or "{" but found "${char}".`,
        );
    }
    return null;
}

export function startingState(scanner) {
    const char = scanner.next();
    if (scanner.isEOF()) {
        scanner.error('SyntaxError', 'Expected "/" but found end of input.');
        return null;
    }
    if (char !== '/') {
        scanner.error('SyntaxError', `Expected "/" but found "${char}".`);
        return null;
    }

    return parseNameSeparator;
}

/**
 * parseKeypath
 *
 * @param {string} string - the keypath to be parsed
 * @param {Object} options
 * @param {string[]} [options.quoteChars=['\'', '"'] - sets which characters
 *     will be treated as quote characters by the parser
 * @param {boolean} [options.initialQuotesOnly] - only try to unquote
 *     a key if a quote character is found at the beginning of the key
 * @param {function} [defaultState=startingState] - set the starting state
 *     of the parser
 *
 * @returns {Object|Array} array of tokens found by the parser or an error
 *     object, with an array of found tokens before the error
 *     added as a property, if the parser encountered an error
 */
export function parseKeypath(
    string,
    {
        defaultState = startingState,
        options,
        funcs: {
            createParser = createScanner,
        } = {},
    } = {},
) {
    if (!isString(string)) {
        const err = new Error();
        err.name = 'TypeError';
        err.message = 'Invalid input';
        err.offset = -1;
        err.tokens = [];
        throw err;
    }

    const opts = {
        ...options,
    };

    if (!Array.isArray(opts.quoteChars)) {
        opts.quoteChars = ['\'', '"'];
    }

    const scanner = createParser(string, defaultState, opts);

    const { errors, tokens } = scanner.run();

    if (errors.length > 0) {
        const [error] = errors;
        const err = new Error();
        err.message = error.message;
        err.name = error.name;
        err.offset = error.offset;
        err.tokens = tokens;
        throw err;
    }
    return tokens;
}

export function initialKeypathKeyParserState(scanner) {
    const char = scanner.next();
    if (char === '}') {
        return parseKeyEnd;
    }
    if (scanner.isEOF()) {
        scanner.error('SyntaxError', 'Expected a key but found end of input.');
        return null;
    }
    return parseKey;
}

export function parseKeypathKeys(
    string,
    {
        defaultState = initialKeypathKeyParserState,
        options,
        funcs: {
            parser = parseKeypath,
        } = {},
    } = {},
) {
    let tokens = [];
    try {
        tokens = parser(
            string,
            {
                defaultState,
                options,
            },
        );
    } catch (err) {
        // NOTE(dbildstr): the error should just be an end of input error
        // and can safely be ignored, so just get the tokens from the error
        // object
        tokens = err.tokens;
    }
    return tokens;
}
