    getComponent(nextState, cb) {
        import(/* webpackChunkName: "HighAvailabilityContainer" */'./HighAvailabilityContainer').then((HighAvailabilityContainer) => {
            cb(null, HighAvailabilityContainer.default);
        });
    },
