import React from 'react';
import PropTypes from 'prop-types';
import { invariant } from '@webui-one/core/utils/functional';
import RouterContext from './RouteContext';
import { matchPath } from '../router-utils/match';

class Route extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    componentDidMount() {
        this.routeIsMounted = true;
    }
    componentWillUnmount() {
        this.routeIsMounted = false;
    }

    getComponent(router) {
        const { component, getComponent } = this.props;
        const { asyncComponent } = this.state;
        if (component) {
            return component;
        }
        if (asyncComponent) {
            return asyncComponent;
        }
        if (typeof getComponent === 'function') {
            getComponent(router, (error, newAsyncComponent) => {
                if (this.routeIsMounted) {
                    if (error) {
                        throw error;
                    }
                    this.setState({ asyncComponent: newAsyncComponent });
                }
            });
            return null;
        }
        return undefined;
    }

    render() {
        return (
            <RouterContext.Consumer>
                {(context) => {
                invariant(context, 'You should not use a <Route> outside a <Router>');
                const { children, computedMatch, exact, path } = this.props;
                const { location, router } = context;
                const routePath = path || '';
                const match = computedMatch || matchPath(location, routePath, exact);
                if (!match) {
                    return null;
                }
                const component = this.getComponent(router);
                if (component === undefined) {
                    return children;
                }
                if (component === null) {
                    return null;
                }
                const props = {
                    ...context,
                    ...match,
                    route: this,
                };
                return React.createElement(component, props);
            }}
            </RouterContext.Consumer>
        );
    }
}

Route.propTypes = {
    children: PropTypes.node,
    component: PropTypes.elementType,
    computedMatch: PropTypes.shape({}),
    exact: PropTypes.bool,
    getComponent: PropTypes.func,
    path: PropTypes.oneOfType([
        PropTypes.func,
        PropTypes.string,
    ]),
};

export default Route;
