import React from 'react';
import PropTypes from 'prop-types';
import RouteProvider from './RouteProvider';
import createTransactionManager from '../router-utils/transactionManager';
import { assignRouterState, createRouterObject } from '../router-utils/routerUtils';

class Router extends React.Component {
    constructor(props) {
        super(props);
        this.handleHistory = this.handleHistory.bind(this);
        this.state = {
            initialized: false,
        };
        const {
            createRouterObjectFunc,
            createTransactionManagerFunc,
            history,
        } = props;
        this.trans = createTransactionManagerFunc(history);
        this.router = createRouterObjectFunc(history, this.state);
    }

    componentDidMount() {
        this.unlisten = this.trans.listen(this.handleHistory);
    }

    componentWillUnmount() {
        this.unlisten();
    }

    handleHistory(historyState) {
        const { assignRouterStateFunc, onUpdate } = this.props;
        assignRouterStateFunc(this.router, historyState);
        this.setState(historyState, onUpdate);
    }

    render() {
        const {
            children,
        } = this.props;
        const {
            location,
            params,
            routeParams,
        } = this.state;
        const context = {
            location,
            params,
            router: this.router,
            routeParams,
        };
        return (
            <div data-handle="webui-one-router">
                <RouteProvider context={context}>
                    {children}
                </RouteProvider>
            </div>
        );
    }
}

Router.propTypes = {
    assignRouterStateFunc: PropTypes.func,
    children: PropTypes.node,
    createRouterObjectFunc: PropTypes.func,
    createTransactionManagerFunc: PropTypes.func,
    history: PropTypes.shape({}),
    onUpdate: PropTypes.func,
};

Router.defaultProps = {
    assignRouterStateFunc: assignRouterState,
    createRouterObjectFunc: createRouterObject,
    createTransactionManagerFunc: createTransactionManager,
};

export default Router;
