import { formatPath } from '@webui-one/core/utils/path';

export const PACKAGE_UPGRADE_NAME = 'PackageUpgrade';

export default {
    path: () => formatPath(PACKAGE_UPGRADE_NAME),
    getComponent(nextState, cb) {
        import(/* webpackChunkName: "PackageUpgrade" */'./PackageUpgrade').then((PackageUpgrade) => {
            cb(null, PackageUpgrade.default);
        }, () => undefined)
            .catch(() => undefined);
    },
};
