import { isDeepEqual } from '@webui-one/core/utils/object';
import { locationChangeAction } from './actions';
import { selectLocationState } from './selectors';

const sync = (history, store) => {
    let currentLocation;

    if (selectLocationState(store.getState()) === undefined) {
        throw new Error('routing not mounted in store');
    }

    const getLocationInStore = () => {
        const locationState = selectLocationState(store.getState());
        return locationState.locationBeforeTransitions;
    };

    const handleLocationChange = (location) => {
        if (!isDeepEqual(currentLocation, location)) {
            currentLocation = location;
            store.dispatch(locationChangeAction(location));
        }
    };

    const unsubscribe = history.listen(handleLocationChange);

    handleLocationChange(history.getCurrentLocation());

    const listen = (listener) => {
        let lastPublishedLocation = getLocationInStore();
        let unsubscribed = false;
        const unsubscribeListenerFromStore = store.subscribe(() => {
            const currentListenerLocation = getLocationInStore();
            if (currentListenerLocation === lastPublishedLocation) {
                return;
            }
            lastPublishedLocation = currentListenerLocation;
            if (!unsubscribed) {
                listener(lastPublishedLocation);
            }
        });

        return () => {
            unsubscribed = true;
            unsubscribeListenerFromStore();
        };
    };

    return {
        ...history,
        listen,
        unsubscribe,
    };
};

export default sync;
