/* eslint-disable import/prefer-default-export */
import { isPositiveNumber } from '@webui-one/core/utils/number';
import { isNonEmptyArray } from '@webui-one/core/utils/array';

export const isShowConfigList = list => (
    isNonEmptyArray(list)
    && list[0] === null
    && isPositiveNumber(list[1])
);
