/* eslint-disable import/prefer-default-export */
import { getCases as _getCases } from '@webui-one/core/jsonrpc';

import { read } from 'modules/transactions/actions';
import {
    runCustomActionThunk as _runCustomActionThunk,
} from 'modules/yangActions/actions';

import { packageStatusUrl } from 'modules/packages/utils';
import { selectWebuiPackages } from 'modules/systemSetting/reducer';

import { query } from 'modules/nodes/actions';
import { getParentPath } from '@webui-one/core/utils/path';
import { zipWithProp } from '@webui-one/core/utils/array';
import * as actionTypes from './actionTypes';
import {
    ERROR_INFO_SELECTOR,
    RELOAD_PATH,
} from './paths';

export const packageStatusAction = packages => ({
    type: actionTypes.PACKAGE_STATUS,
    packages,
});

export const packageWebuiAction = packages => ({
    type: actionTypes.PACKAGE_WEBUI,
    packages,
});

export const packageGetCompleteAction = packages => ({
    type: actionTypes.PACKAGES_GET,
    packages,
});

export const getPackagesCompleteAction = () => ({
    type: actionTypes.PACKAGES_GET_COMPLETE,
});

export const getPackagesErrorAction = error => ({
    type: actionTypes.PACKAGES_GET_ERROR,
    error,
});

export const getPackagesStartAction = () => ({
    type: actionTypes.PACKAGES_GET_START,
});

export const reloadPackages = ({
    funcs: {
        runCustomActionThunk = _runCustomActionThunk,
    } = {},
} = {}) => dispatch => dispatch(runCustomActionThunk({ actionPath: RELOAD_PATH }));


export const getPackagesAsyncAction = () => ({
    type: actionTypes.PACKAGES_GET_ASYNC,
});

const findWebuiPackages = packages => (
    dispatch => (
        Promise.resolve(dispatch(packageWebuiAction(packages)))
    )
);

const getPackageStatus = (
    packages,
    {
        funcs: {
            getCases = _getCases,
            readThunk = read,
        } = {},
    } = {},
) => (dispatch) => {
    const choices = packages.map(packageName => ({
        path: getParentPath(packageStatusUrl(packageName)),
        choice: 'status',
    }));
    return dispatch(readThunk())
        .then(({ th }) => getCases({ choices, th }))
        .then((results) => {
            const packageStatus = results.reduce((acc, result, index) => {
                acc[packages[index]] = result.result.case;
                return acc;
            }, {});
            dispatch(packageStatusAction(packageStatus));
        });
};

function getPackages({
    funcs: {
        queryFunc = query,
        getPackageStatusFunc = getPackageStatus,
        findWebuiPackagesFunc = findWebuiPackages,
    } = {},
} = {}) {
    const selection = [
        'name',
        'description',
        'package-version',
        ERROR_INFO_SELECTOR,
    ];
    const path = '/ncs:packages/package';

    return async (dispatch, getState) => {
        try {
            const action = await dispatch(queryFunc({ selection, path }));
            if (action.result === undefined) return undefined;
            dispatch(getPackagesStartAction());
            const packages = zipWithProp(action.result.results, 'name');
            dispatch(packageGetCompleteAction(packages));
            const packageNames = Object.keys(packages);
            await dispatch(getPackageStatusFunc(packageNames));
            dispatch(findWebuiPackagesFunc(selectWebuiPackages(getState())));
            return dispatch(getPackagesCompleteAction());
        } catch (error) {
            return dispatch(getPackagesErrorAction(error));
        }
    };
}

export {
    findWebuiPackages,
    getPackages,
    getPackageStatus,
};
