import {
    pathWithoutKeyInstances,
} from '@webui-one/core/keypath/kp-parser';

import { toNedKindpath } from '@webui-one/core/kindpath';
import { omitKeys } from '@webui-one/core/utils/object';
import { RE_DEVICES_DEVICE_CONFIG_CHILD } from '@webui-one/core/utils/regexps';

import { getName, itemTypes } from '../utils';

const createStaticSchemaData = schema => omitKeys(
    schema.data,
    [
        'access',
        'active',
        'cases',
        'children',
        'evaluated_when_entry',
        'exists',
        'leafrefGroups',
        'leafref_groups',
        'value',
    ],
);

const createStaticSchemaPath = (path, nedId) => {
    const keyInstancePath = pathWithoutKeyInstances(path);
    const match = RE_DEVICES_DEVICE_CONFIG_CHILD.exec(keyInstancePath);

    if (match !== null && nedId) {
        return `/${nedId}/${match[2]}`;
    }

    if (match !== null) {
        throw new Error(`ned-id is missing for path: ${path}`);
    }

    return keyInstancePath;
};


export const createStaticSchema = (rawSchema) => {
    const {
        kindpath,
        nedId,
        ...rest
    } = omitKeys(rawSchema, ['hasEvaluatedWhenEntries']);
    const path = createStaticSchemaPath(rawSchema.path, nedId);

    const schema = {
        ...rest,
        path,
        data: createStaticSchemaData(rawSchema),
        itemType: itemTypes.STATIC_SCHEMA,
    };
    if (kindpath) schema.kindpath = toNedKindpath(kindpath, nedId);

    if (rawSchema.data.children && rawSchema.data.children.length > 0) {
        schema.children = rawSchema.data.children.map(c => getName(path)({ data: c }));
    }

    if (nedId) {
        const nedSchema = {
            data: {},
            itemType: itemTypes.EVALUATED_NED_SCHEMA,
            nedId,
            path: rawSchema.path,
        };
        if (kindpath) nedSchema.kindpath = kindpath;
        return [nedSchema, schema];
    }

    return [schema];
};

export {
    createStaticSchemaPath as _createStaticSchemaPath,
    createStaticSchemaData as _createStaticSchemaData,
};
