import * as actionTypes from './actionTypes';
import { ERROR_INFO_SELECTOR } from './paths';

const updatePackages = (state, pkgs) => {
    const prefixes = Object.keys(pkgs);
    const newPackages = prefixes.reduce((acc, prefix) => {
        const pkg = pkgs[prefix];
        const version = pkg['package-version'] || 'N/A';
        acc[prefix] = {
            ...acc[prefix],
            description: pkg.description,
            errorInfo: pkg[ERROR_INFO_SELECTOR],
            name: pkg.name,
            status: pkg.status,
            version,
        };
        return acc;
    }, {});

    return {
        ...state,
        packages: newPackages,
    };
};

const initialState = {
    packages: {},
    loading: false,
};

const getPackage = (state, packageName) =>
    state.packages && state.packages[packageName];

const updateWebuiPackages = (state, action) => {
    const newPackages = action.packages.reduce((acc, packageName) => {
        acc[packageName] = {
            ...getPackage(state, packageName),
            webui: true,
        };
        return acc;
    }, {});
    return {
        ...state,
        packages: {
            ...state.packages,
            ...newPackages,
        },
    };
};

const updatePackageStatus = (state, action) => {
    const newPackages = Object.entries(action.packages)
        .reduce((acc, [packageName, status]) => {
            acc[packageName] = {
                ...getPackage(state, packageName),
                status,
            };
            return acc;
        }, {});
    return {
        ...state,
        packages: {
            ...state.packages,
            ...newPackages,
        },
    };
};

export default function packages(state = initialState, action = {}) {
    switch (action.type) {
    case actionTypes.PACKAGE_WEBUI:
        return updateWebuiPackages(state, action);
    case actionTypes.PACKAGE_STATUS:
        return updatePackageStatus(state, action);
    case actionTypes.PACKAGES_GET:
        return updatePackages(state, action.packages);
    case actionTypes.PACKAGES_GET_COMPLETE:
        return {
            ...state,
            loading: false,
        };
    case actionTypes.PACKAGES_GET_START:
        return {
            ...state,
            loading: true,
        };
    default:
        return state;
    }
}

const EMPTY_OBJECT = Object.freeze({});
export const selectPackageState = state => state.packages || EMPTY_OBJECT;
export const selectPackages = state => selectPackageState(state).packages;
export const selectLoadingPackages = state => selectPackageState(state).loading;
