import * as actionTypes from './actionTypes';

const initialState = {
    alarms: {},
    summary: {},
};

const reducer = (state = initialState, action = {}) => {
    switch (action.type) {
    case actionTypes.GET_ALARMS_START: {
        return {
            ...state,
            alarms: {
                ...state.alarms,
                isLoading: true,
            },
        };
    }
    case actionTypes.GET_ALARMS_COMPLETE: {
        return {
            ...state,
            alarms: {
                ...state.alarms,
                isLoading: false,
                sortColumn: action.columnKey,
                sortOrder: action.sortOrder,
                ...action.alarmList,
            },
        };
    }
    case actionTypes.GET_ALARMS_ERROR: {
        return {
            ...state,
            alarms: {
                ...state.alarms,
                isLoading: false,
                ...action.error,
            },
        };
    }
    case actionTypes.SET_EXPANDED_ROW: {
        return {
            ...state,
            alarms: {
                ...state.alarms,
                expandedRows: {
                    ...state.alarms.expandedRows,
                    [action.path]: action.isExpanded,
                },
            },
        };
    }
    case actionTypes.GET_ALARM_SUMMARY_START: {
        return {
            ...state,
            summary: {
                ...state.summary,
                isLoading: true,
            },
        };
    }
    case actionTypes.GET_ALARM_SUMMARY_COMPLETE: {
        return {
            ...state,
            summary: {
                summary: action.values,
                isLoading: false,
            },
        };
    }
    case actionTypes.GET_ALARM_SUMMARY_ERROR: {
        return {
            ...state,
            summary: {
                ...action.error,
                isLoading: false,
            },
        };
    }
    case actionTypes.SHOW_CLEARED_ALARMS: {
        return {
            ...state,
            alarms: {
                ...state.alarms,
                showClearedAlarms: !state.alarms.showClearedAlarms,
            },
        };
    }
    case actionTypes.SET_SELECTED_ALARM: {
        return {
            ...state,
            alarms: {
                ...state.alarms,
                selectedAlarm: action.severity,
            },
        };
    }
    case actionTypes.SELECT_ALARM_ROWS: {
        return {
            ...state,
            alarms: {
                ...state.alarms,
                selectedRows: action.alarmNames.reduce((acc, alarmName) => ({
                    ...acc,
                    [alarmName]: {
                        isSelected: action.isSelected,
                    },
                }), state.alarms.selectedRows),
            },
        };
    }
    case actionTypes.GET_DEVICES_START: {
        return {
            ...state,
            devices: {
                ...state.devices,
                isLoading: true,
            },
        };
    }
    case actionTypes.GET_DEVICES_COMPLETE: {
        return {
            ...state,
            devices: {
                devices: action.devices,
                isLoading: false,
            },
        };
    }
    case actionTypes.GET_DEVICES_ERROR: {
        return {
            ...state,
            devices: {
                ...action.error,
                isLoading: false,
            },
        };
    }
    default:
        return state;
    }
};

export default reducer;
