import React from 'react';
import PropTypes from 'prop-types';
import { invariant, isFunction } from '@webui-one/core/utils/functional';
import { ensureArray } from '@webui-one/core/utils/array';
import RouterContext from './RouteContext';
import { matchPath } from '../router-utils/match';

const findRoute = (children, location) => (
    ensureArray(children).reduce((acc, element) => {
        if (acc || !React.isValidElement(element)) {
            return acc;
        }
        const path = element.props.path || element.props.from || '';
        const routePath = isFunction(path) ? path() : path;
        const exact = element.props.exact;
        const computedMatch = matchPath(location, routePath, exact);
        const key = element.key || routePath;
        if (!computedMatch) {
            return acc;
        }
        return {
            element,
            computedMatch,
            key,
        };
    }, null) || {}
);

const Switch = ({ children }) => (
    <RouterContext.Consumer>
        {(context) => {
            invariant(context, 'You should not use a <Switch> outside a <Router>');
            const { location } = context;

            const {
                computedMatch,
                element,
                key,
            } = findRoute(children, location);

            if (!element) {
                return null;
            }

            return React.cloneElement(
                element,
                {
                    location,
                    computedMatch,
                    key,
                },
            );
        }}
    </RouterContext.Consumer>
);

Switch.propTypes = {
    children: PropTypes.node,
};

export default Switch;
