import {
    jsonrpcBatch as _jsonrpcBatch,
} from '@webui-one/core/jsonrpc';
import { getSystemSetting as _getSystemSetting } from 'modules/systemSetting/actions';
import { read as _read } from 'modules/transactions/actions';
import { isNonEmptyArray, zipObject } from '@webui-one/core/utils/array';
import { delay } from '@webui-one/core/utils/async';
import { pathOr } from '@webui-one/core/utils/object';
import { pipe } from '@webui-one/core/utils/functional';
import * as actionTypes from './actionTypes';
import { selectModels, selectRootModelsLoading } from './reducer';

export const getModelsCompleteAction = models =>
    ({ type: actionTypes.GET_MODELS_COMPLETE, models });
export const getModelsStartAction = () => ({ type: actionTypes.GET_MODELS_START });
export const getModelsErrorAction = error => ({ type: actionTypes.GET_MODELS_ERROR, error });
export const setModelExpandedAction = (modelName, expanded) =>
    ({ type: actionTypes.SET_MODEL_EXPANDED, modelName, expanded });


const createRequest = th => namespace => ({
    method: 'get_schema',
    params: {
        th,
        levels: 0,
        namespace,
        evaluate_when_entries: true,
    },
});

const zippedToModelsAndErrors = zipped => Object.keys(zipped).reduce((acc, cur) => {
    const result = zipped[cur];
    if ('error' in result) {
        acc.errors[cur] = result.error;
        return acc;
    }

    const children = pathOr([], ['result', 'data', 'children'], result);
    acc.models = [
        ...acc.models,
        ...children.map(c => c.qname || c.name),
    ];
    return acc;
}, { models: [], errors: {} });

const createGetModels = ({
    namespaces,
    funcs: {
        jsonrpcBatch = _jsonrpcBatch,
    } = {},
} = {}) => pipe(
    th => namespaces.map(createRequest(th)),
    requests => jsonrpcBatch({ requests }),
    results => zipObject(namespaces, results),
    zippedToModelsAndErrors,
);

const getNamespaces = ({ systemSetting: { models = [] } } = {}) => (
    models.map(m => m.namespace)
);

export const getModelsThunk = ({
    funcs: {
        createGetModelsFunc = createGetModels,
        getSystemSetting = _getSystemSetting,
        read = _read,
    } = {},
} = {}) => async (dispatch) => {
    try {
        const result = await dispatch(getSystemSetting());
        if (result === undefined) {
            return delay(500)().then(() => {
                dispatch(getModelsThunk());
            });
        }
        dispatch(getModelsStartAction());
        const { th } = await dispatch(read());

        const namespaces = getNamespaces(result);
        const { models } = await createGetModelsFunc({ namespaces })(th);
        return dispatch(getModelsCompleteAction(models));
    } catch (error) {
        return dispatch(getModelsErrorAction(error));
    }
};

export const maybeGetModels = ({
    funcs: {
        getModels = getModelsThunk,
    } = {},
} = {}) => (
    (dispatch, getState) => {
        const state = getState();
        const models = selectModels(state);

        if (selectRootModelsLoading(state) || isNonEmptyArray(models)) {
            return Promise.resolve();
        }

        return dispatch(getModels());
    }
);

export {
    createGetModels as _createGetModels,
};
