/* eslint-disable import/prefer-default-export */
import { isObject } from './object';
import { isString } from './string';
import { isFunction } from './functional';

export function getCharacter(string, offset) {
    const code = string.codePointAt(offset);
    const char = Number.isFinite(code)
        ? String.fromCodePoint(code)
        : undefined;
    // JavaScript characters are UTF-16, so we need to calculate the start of
    // the next character in the string
    let width = 0;
    if (Number.isFinite(code)) {
        width = code > 0xFFFF ? 2 : 1;
    }
    return {
        char,
        code,
        width,
    };
}

export class Scanner {
    constructor(input, state, options) {
        this.options = isObject(options) ? options : {};
        this.state = state;
        this.src = isString(input) ? input : '';
        this.tokens = [];
        this.errors = [];
        this.offset = 0;
        this.rdOffset = 0;
        this.start = 0;
        this.width = 0;
        this.char = undefined;
    }

    current() {
        return this.char;
    }

    emit(type, data) {
        const text = this.src.slice(this.start, this.rdOffset);
        this.tokens.push({ type, text, offset: this.start, ...data });
        this.start = this.rdOffset;
    }

    error(name, message) {
        this.errors.push({ name, message, offset: this.start });
    }

    getErrors() {
        return this.errors;
    }

    getOption(key, defaultOption) {
        if (key in this.options) return this.options[key];
        return defaultOption;
    }

    getTokens() {
        return this.tokens;
    }

    hasOption(key, value) {
        return this.options[key] === value;
    }

    ignore() {
        this.start = this.offset;
    }

    isEOF() {
        return this.offset === -1;
    }

    next() {
        if (this.rdOffset < this.src.length) {
            this.width = this.rdOffset - this.offset;
            this.offset = this.rdOffset;
            const { char, code, width } = getCharacter(this.src, this.offset);
            this.rdOffset += width;
            this.char = char;
            this.code = code;
        } else {
            this.char = undefined;
            this.code = undefined;
            this.offset = -1;
        }
        return this.char;
    }

    peek() {
        if (this.rdOffset < this.src.length) {
            const { char } = getCharacter(this.src, this.rdOffset);
            return char;
        }
        return undefined;
    }

    run() {
        while (isFunction(this.state)) {
            this.state = this.state(this);
        }
        return {
            errors: this.errors,
            tokens: this.tokens,
        };
    }
}

export function createScanner(input, initialState, options) {
    return new Scanner(input, initialState, options);
}
