import React from 'react';
import PropTypes from 'prop-types';

import RouteContext from './RouteContext';
import { LocationPropType } from './router-prop-types';

import { createLocation } from '../router-utils/routerUtils';
import { MOUSE_BUTTON } from '@webui-one/core/utils/browser';
import { invariant, isFunction } from '@webui-one/core/utils/functional';
import { isString } from '@webui-one/core/utils/string';
import { createPathFromLocation } from '@webui-one/core/utils/path';

export const resolveToLocation = (to, currentLocation) => (
    isFunction(to) ? to(currentLocation) : to
);

export const normalizeLocation = (
    to,
    currentLocation,
    { createLocationFunc = createLocation } = {},
) => (
    isString(to) ? createLocationFunc(to, null, null, currentLocation) : to
);

const isModifiedEvent = evt => (
    !!(evt.metaKey || evt.altKey || evt.ctrlKey || evt.shiftKey)
);

export const shouldNavigateRoute = (evt, target, to) => !!(
    !evt.defaultPrevented
    && evt.button === MOUSE_BUTTON.PRIMARY
    && (!target || target === '_self')
    && !isModifiedEvent(evt)
    && to
);

class Link extends React.Component {
    constructor(props) {
        super(props);
        this.onClick = this.onClick.bind(this);
        this.state = {};
    }

    onClick(evt, { shouldNavigate = shouldNavigateRoute } = {}) {
        const { onClick, target, to } = this.props;
        const router = this.getRouterFromContext();
        if (isFunction(onClick)) {
            try {
                onClick(evt);
            } catch (err) {
                evt.preventDefault();
                throw err;
            }
        }
        if (shouldNavigate(evt, target, to)) {
            evt.preventDefault();
            const href = resolveToLocation(to, router.location);
            const path = createPathFromLocation(router.location);
            if (href !== path) {
                router.push(href);
            }
        }
    }

    getRouterFromContext() {
        const { router } = this.context || {};
        invariant(
            router,
            '<Link>s rendered outside of a router context cannot navigate.',
        );
        return router;
    }

    render() {
        return (
            <RouteContext.Consumer>
                {(context) => {
                this.context = context;
                const router = this.getRouterFromContext();
                const {
                    to,
                    ...rest
                } = this.props;
                const toLocation = normalizeLocation(
                    resolveToLocation(to, router.location),
                    router.location,
                );
                const href = toLocation ? router.createHref(toLocation) : rest.href || '';
                const props = {
                    ...rest,
                    href,
                    onClick: this.onClick,
                };

                /* eslint-disable-next-line jsx-a11y/anchor-has-content */
                return <a {...props} />;
            }}
            </RouteContext.Consumer>
        );
    }
}

Link.propTypes = {
    children: PropTypes.node,
    href: PropTypes.string,
    onClick: PropTypes.func,
    style: PropTypes.shape({}),
    target: PropTypes.string,
    to: PropTypes.oneOfType([
        PropTypes.string,
        LocationPropType,
    ]),
};

export default Link;
