export const readAction = ({
    th,
    mode = MODES.READ,
    db = DBS.RUNNING,
    tag = TAGS.WEBUI_ONE,
}) => {
    const action = {
        type: actionTypes.READ,
        db,
        th,
        tag,
        mode,
    };

    if (mode === MODES.READ) {
        action.read = th;
    }

    if (mode === MODES.READ_WRITE) {
        action.read_write = th;
    }
    return action;
};

