const tryToResolveConflicts = ({
    th,
    error,
    funcs: {
        jsonRpcFunc = jsonrpcSimple,
    } = {},
} = {}) => (
    async (dispatch) => {
        if (error && error.message === 'A conflict must be resolved') {
            const conflicts = await jsonRpcFunc('get_trans_conflicts', { th });
            dispatch(transConflictsAction(th, conflicts.conflicts));
        }
    }
);

const validateTrans = (
    th,
    {
        funcs: {
            jsonrpc = jsonrpcSimple,
        } = {},
    } = {},
) => (
    async (dispatch) => {
        try {
            const response = await jsonrpc('validate_trans', { th });
            return dispatch(validateTransAction(th, response));
        } catch (error) {
            await dispatch(tryToResolveConflicts({ th, error }));
            return dispatch(validateTransErrorAction(th, error));
        }
    }
);

const reevaluate = ({
    th,
    funcs: {
        getTransChangesFunc = getTransChanges,
        validateTransFunc = validateTrans,
    } = {},
} = {}) => (
    async (dispatch, getState) => {
        if (selectThIsBusy(getState())) return Promise.resolve();
        try {
            dispatch(reevaluateStartAction(th));
            const transChanges = await dispatch(getTransChangesFunc({ th }));
            if (transChanges.changes.length === 0) {
                return dispatch(reevaluateErrorAction(th));
            }
            const validTrans = await dispatch(validateTransFunc(th));
            if (validTrans.type === actionTypes.VALIDATE_TRANS_ERROR) {
                return dispatch(reevaluateErrorAction(th));
            }

            return dispatch(reevaluateCompleteAction(th));
        } catch (error) {
            return dispatch(reevaluateErrorAction(th));
        }
    }
);

export const resolveConflicts = ({
    th,
    funcs: {
        jsonRpcFunc = jsonrpcSimple,
        reevaluateFunc = reevaluate,
    } = {},
} = {}) => (
    async (dispatch) => {
        try {
            dispatch(startResolveConflictsAction(th));

            await jsonRpcFunc('resolve_trans', { th });
            dispatch(reevaluateFunc({ th }));
            dispatch(completeResolveConflictsAction(th));
        } catch (error) {
            dispatch(errorResolveConflictsAction(th));
        }
    }
);

