const validateCommit = ({
    th,
    flags,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => (
    async (dispatch) => {
        try {
            const params = { th };
            if (isNonEmptyArray(flags)) {
                params.flags = flags;
            }
            const response = await jsonrpc('validate_commit', params);
            if ('warnings' in response) {
                return dispatch(validateCommitWarningsAction(th, response.warnings));
            }
            return dispatch(validateCommitAction(th));
        } catch (error) {
            return dispatch(validateCommitErrorAction(th, error));
        }
    }
);

const validateAndCommit = ({
    th,
    flags,
    extendIntervalMs = 600000,
    funcs: {
        extendIdleSession = extendSession,
        validateCommitFunc = validateCommit,
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => (
    async (dispatch) => {
        await dispatch(commitStartAction(th));
        const stopExtendingSession = await extendIdleSession({
            extendIntervalMs,
        });
        await dispatch(validateCommitFunc({ flags, th }));

        try {
            const params = { th, 'rollback-id': true };
            if (flags && flags.length > 0) {
                params.flags = flags;
            }

            const result = await jsonrpc('commit', params);
            stopExtendingSession();
            return dispatch(commitCompleteAction(th, result));
        } catch (error) {
            stopExtendingSession();
            return dispatch(commitErrorAction(th, error));
        }
    }
);

const deleteTrans = (
    th,
    {
        funcs: {
            jsonrpc = jsonrpcSimple,
        } = {},
    } = {},
) => (
    (dispatch, getState) => {
        const state = getState();
        const changes = selectChanges(state);
        const dirtyPaths = selectDirtyPaths(state);
        const status = selectTransactionDeleteStatus(state, th);
        if (status === deleteStatus.DELETING) {
            return Promise.resolve({});
        }
        dispatch(deleteTransStartAction(th));
        return jsonrpc('delete_trans', { th })
            .then(() => dispatch(deleteTransAction(th, changes, dirtyPaths)))
            .catch(error => dispatch(deleteTransErrorAction(th, error)));
    }
);

export const tryDeleteTrans = ({
    th,
    funcs: {
        deleteTransThunk = deleteTrans,
    } = {},
} = {}) =>
    async (dispatch, getState) => {
        const state = getState();
        const status = selectTransactionDeleteStatus(state, th);
        const refCount = selectTransactionRefCount(state, th);
        if (status !== deleteStatus.DELETE_PENDING || refCount !== 0) {
            return Promise.resolve({});
        }
        return dispatch(deleteTransThunk(th));
    };

export const deleteTransPending = (
    th,
    {
        funcs: {
            deleteTransThunk = tryDeleteTrans,
        } = {},
    } = {},
) => (
    async (dispatch, getState) => {
        const status = selectTransactionDeleteStatus(getState(), th);
        if (status === deleteStatus.DELETE_PENDING) {
            return Promise.resolve({});
        }
        dispatch(deleteTransPendingAction(th));
        return dispatch(deleteTransThunk({ th }));
    }
);

// TODO(dbildstr): set rollbacks to empty list before calling get_rollbacks on the backend
const getRollbacks = () => (
    dispatch => (
        jsonrpcSimple('get_rollbacks')
            .then((result) => {
                dispatch(rollbacksAction(result.rollbacks || []));
            })
    )
);

const loadRollback = ({
    nr,
    keypath,
    selective,
    funcs: {
        jsonrpc = jsonrpcSimple,
        reevaluateFunc = reevaluate,
        writeThunk = write,
    } = {},
} = {}) => (
    dispatch => (
        dispatch(writeThunk()).then(({ th }) => {
            dispatch(rollbackStartAction());
            return jsonrpc('load_rollback', { th, nr, path: keypath, selective })
                .then(result => (
                    dispatch(reevaluateFunc({ th })).then(() => {
                        dispatch(rollbackLoadedAction());
                        dispatch(dataLoadedAction());
                        return result;
                    })
                ));
        }).catch(error => dispatch(loadRollbackErrorAction(error)))
    )
);

const readRollback = nr => (
    dispatch => (
        jsonrpcSimple('get_rollback', { nr })
            .then((rollback) => {
                dispatch(rollbackReadAction(nr, rollback.rollback));
                return rollback;
            })
            .catch(error => dispatch(readRollbackErrorAction(error)))
    )
);

const loadFile = ({
    data,
    format = 'xml',
    funcs: {
        jsonrpc = jsonrpcSimple,
        reevaluateFunc = reevaluate,
        writeThunk = write,
    } = {},
} = {}) => (
    dispatch => dispatch(writeThunk()).then(({ th }) => {
        dispatch(loadStartAction());
        return jsonrpc('load', { th, data, format })
            .then(result => (
                dispatch(reevaluateFunc({ th })).then(() => {
                    dispatch(loadFileSuccessAction());
                    dispatch(dataLoadedAction());
                    return result;
                })
            ));
    }).catch(error => dispatch(loadFileErrorAction(error)))
);

const saveFile = ({
    format = 'xml',
    funcs: {
        formattedCommitDryRunFunc = formattedCommitDryRun,
        writeThunk = write,
    } = {},
} = {}) => (
    dispatch => dispatch(writeThunk()).then(({ th }) => {
        dispatch(saveStartAction());
        return dispatch(formattedCommitDryRunFunc({ th, format }))
            .then((result) => {
                if (result.error) {
                    dispatch(saveFileErrorAction(result.error));
                } else {
                    dispatch(saveFileSuccessAction());
                }
                return result;
            });
    }).catch(error => dispatch(
        saveFileErrorAction(error && error.error ? error.error : error),
    ))
);

