import { isNonEmptyString } from '@webui-one/core/utils/string';
import { last, zipObject } from '@webui-one/core/utils/array';

const matchingPattern = /:([a-zA-Z_$][a-zA-Z0-9_$]*)|\*\*|\*|\(|\)|\\\(|\\\)/g;
const escapeRegExp = string => string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');

const patternCache = {};

export const patternCompiler = (pattern) => {
    const paramNames = [];
    const source = [];
    const tokens = [];
    let lastIndex = 0;
    let match;

    // eslint-disable-next-line no-cond-assign
    while ((match = matchingPattern.exec(pattern))) {
        const [token, param] = match;

        if (match.index !== lastIndex) {
            tokens.push(pattern.slice(lastIndex, match.index));
            source.push(escapeRegExp(pattern.slice(lastIndex, match.index)));
        }

        if (param) {
            source.push('([^/]+)');
            paramNames.push(param);
        } else if (token === '**') {
            source.push('(.*)');
            paramNames.push('splat');
        } else if (token === '*') {
            source.push('(.*?)');
            paramNames.push('splat');
        } else if (token === '(') {
            source.push('(?:');
        } else if (token === ')') {
            source.push(')?');
        } else if (token === '\\(') {
            source.push('\\(');
        } else if (token === '\\)') {
            source.push('\\)');
        }

        tokens.push(token);
        lastIndex = matchingPattern.lastIndex;
    }

    if (lastIndex !== pattern.length) {
        tokens.push(pattern.slice(lastIndex, pattern.length));
        source.push(escapeRegExp(pattern.slice(lastIndex, pattern.length)));
    }

    return {
        paramNames,
        source,
        tokens,
    };
};

export const compilePattern = (
    pattern,
    {
        getCache = () => patternCache,
    } = {},
) => {
    const cache = getCache();
    if (!cache[pattern]) {
        cache[pattern] = patternCompiler(pattern);
    }
    return cache[pattern];
};

export const matchPathToPattern = (pathname, src) => (
    pathname.match(new RegExp(`^${src}`, 'i'))
);

export const matchPattern = (
    pattern,
    pathname,
    {
        compile = compilePattern,
        patternMatcher = matchPathToPattern,
    } = {},
) => {
    if (!pattern) {
        return null;
    }
    const { paramNames, tokens, source } = compile(pattern);

    let src = source.join('');

    if (!pattern.endsWith('/')) {
        src += '/?';
    }
    if (last(tokens) === '*') {
        src += '$';
    }

    const match = patternMatcher(pathname, src);
    if (!match) {
        return null;
    }
    const matchedPath = match[0];
    let remaining = pathname.substr(matchedPath.length);
    if (remaining) {
        if (!matchedPath.endsWith('/')) {
            return null;
        }
        remaining = `/${remaining}`;
    }
    const paramValues = match.slice(1).map(v => v && decodeURIComponent(v));
    return {
        paramNames,
        paramValues,
        remaining,
    };
};

export const matchPath = (
    location,
    path,
    {
        matcher = matchPattern,
    } = {},
) => {
    if (!location) {
        return null;
    }
    if (!path) {
        return null;
    }
    const matched = matcher(path, location.pathname);
    if (!matched || isNonEmptyString(matched.remaining)) {
        return null;
    }
    return {
        path: location.pathname,
        routeParams: zipObject(matched.paramNames, matched.paramValues),
    };
};
