import * as actionTypes from './actionTypes';

const initialState = {
    loading: false,
    models: [],
};

export default function models(state = initialState, action = {}) {
    switch (action.type) {
    case actionTypes.GET_MODELS_START:
        return { ...state, loading: true };
    case actionTypes.GET_MODELS_COMPLETE:
        return { ...state, models: action.models, loading: false };
    case actionTypes.GET_MODELS_ERROR:
        return { ...state, loading: false };
    case actionTypes.SET_MODEL_EXPANDED:
        return {
            ...state,
            expandedModels: {
                ...state.expandedModels,
                [action.modelName]: action.expanded,
            },
        };
    default:
        return state;
    }
}

export const selectModels = state => state.models && state.models.models;

export const selectExpandedModels = state => state.models && state.models.expandedModels;

export const selectRootModelsLoading = state => (state.models && state.models.loading) || false;

export const selectRootModels = state => selectModels(state) || [];
