import * as actionTypes from './actionTypes';
import * as ipcActionTypes from 'modules/ipc/actionTypes';
import * as modelsActionTypes from 'modules/models/actionTypes';
import * as packagesActionTypes from 'modules/packages/actionTypes';

export const initialState = {
    includeSubfolders: false,
    isEditMode: false,
    showActions: true,
    showEmpty: true,
    showOperdata: true,
};

export const loadStatus = {
    NOT_LOADED: 0,
    LOADING: 1,
    PACKAGES: 2,
    MODELS: 4,
    MODELS_AND_PACKAGES: 7,
};

export default function reducer(state = initialState, action = {}) {
    switch (action.type) {
    case actionTypes.INCLUDE_SUBFOLDERS:
        return {
            ...state,
            includeSubfolders: action.value,
        };
    case actionTypes.SHOW_ACTIONS:
        return {
            ...state,
            showActions: action.value,
        };
    case actionTypes.SHOW_EMPTY:
        return {
            ...state,
            showEmpty: action.value,
        };
    case actionTypes.SHOW_OPERDATA:
        return {
            ...state,
            showOperdata: action.value,
        };
    case actionTypes.SET_EDIT_MODE:
        return {
            ...state,
            isEditMode: action.value,
        };
    case actionTypes.SHOW_ALL:
        return {
            ...state,
            showOperdata: action.value,
            showEmpty: true,
            showActions: action.value,
        };
    case actionTypes.GET_MODELS_AND_PACKAGES:
        return {
            ...state,
            loadingStatus: loadStatus.LOADING,
        };
    case packagesActionTypes.PACKAGES_GET_COMPLETE:
    case packagesActionTypes.PACKAGES_GET_ERROR:
        return {
            ...state,
            loadingStatus: state.loadingStatus + loadStatus.PACKAGES,
        };
    case modelsActionTypes.GET_MODELS_COMPLETE:
    case modelsActionTypes.GET_MODELS_ERROR:
        return {
            ...state,
            loadingStatus: state.loadingStatus + loadStatus.MODELS,
        };
    case ipcActionTypes.IPC_MODIFICATION:
        return {
            ...state,
            ipcModification: {
                path: action.path,
            },
        };
    default:
        return state;
    }
}

export const selectApplicationConfiguration = state => state.applicationConfiguration;
export const selectIsEditMode = state => state.applicationConfiguration.isEditMode;
export const selectShowEmpty = state => state.applicationConfiguration.showEmpty;
export const selectShowOperdata = state =>
    state.applicationConfiguration.showOperdata && !!selectShowEmpty(state);
export const selectShowActions = state =>
    state.applicationConfiguration.showActions && !!selectShowEmpty(state);
export const selectIncludeSubfolders = state =>
    state.applicationConfiguration.includeSubfolders;
export const selectIpcModification = state =>
    state.applicationConfiguration.ipcModification;
export const selectLoadingStatus = state =>
    state.applicationConfiguration.loadingStatus || loadStatus.NOT_LOADED;
export const selectHasLoaded = state =>
    selectLoadingStatus(state) === loadStatus.MODELS_AND_PACKAGES;
export const selectIsLoading = state =>
    selectLoadingStatus(state) === loadStatus.LOADING
    || selectLoadingStatus(state) === loadStatus.MODELS
    || selectLoadingStatus(state) === loadStatus.PACKAGES;
