import { isShowConfigList } from '@webui-one/core/jsonrpc/utils';
import jsonrpcSimple, {
    findLastTransaction,
    query as _query,
    runAction as _runAction,
    showConfigJson2 as _showConfig,
    zipQueryResult,
} from '@webui-one/core/jsonrpc';

import {
    getDataThunk as _getDataThunk,
} from 'modules/data/actions';

import {
    deleteConfigTransactionsThunk as _deleteConfigTransactionsThunk,
    deleteTransPending as _deleteTrans,
    getTrans as _getTrans,
    newTrans as _newTrans,
    read as _read,
    write,
} from 'modules/transactions/actions';

import { getTimestamp } from '@webui-one/core/utils/time';
import Messages from 'modules/intl/messages';
import { translate } from 'modules/intl/format';

import {
    doConfirm as _doConfirm,
} from 'Common/Confirm/actions';

import { selectYangActionPathsByParent } from './selectors';

import * as actionTypes from './actionTypes';

export const keepInputParamsAction = actionPath => ({
    type: actionTypes.KEEP_INPUT_PARAMS,
    actionPath,
});

export const runActionAbortAction = (actionPath, getTimestampFun = getTimestamp) => ({
    type: actionTypes.RUN_ACTION_ABORT,
    actionPath,
    completedAt: getTimestampFun(),
});

export const resetActionCompleteAction = ({
    newTh,
    oldTh,
    actionPath,
}) => ({
    type: actionTypes.RESET_ACTION_COMPLETE,
    actionPath,
    newTh,
    oldTh,
});

export const resetActionErrorAction = (actionPath, error) => ({
    type: actionTypes.RESET_ACTION_ERROR,
    actionPath,
    error,
});

export const resetActionStartAction = actionPath => ({
    type: actionTypes.RESET_ACTION_START,
    actionPath,
});

export const runActionCompleteAction = ({
    newTh,
    oldTh,
    actionPath,
    result,
    getTimestampFun = getTimestamp,
}) => ({
    type: actionTypes.RUN_ACTION_COMPLETE,
    actionPath,
    completedAt: getTimestampFun(),
    newTh,
    oldTh,
    result,
});

export const runCustomActionCompleteAction = ({
    actionPath,
    newTh,
    oldTh,
    result,
    getTimestampFun = getTimestamp,
}) => ({
    type: actionTypes.RUN_CUSTOM_ACTION_COMPLETE,
    actionPath,
    completedAt: getTimestampFun(),
    newTh,
    oldTh,
    result,
});


export const runActionErrorAction = (actionPath, error, getTimestampFun = getTimestamp) => ({
    type: actionTypes.RUN_ACTION_ERROR,
    actionPath,
    completedAt: getTimestampFun(),
    error,
});

export const runActionStartAction = (actionPath, getTimestampFun = getTimestamp) => ({
    type: actionTypes.RUN_ACTION_START,
    actionPath,
    startedAt: getTimestampFun(),
    completedAt: undefined,
});

export const clearActionOutputAction = actionPath => ({
    type: actionTypes.CLEAR_ACTION_OUTPUT,
    actionPath,
});

export const clearActionAction = actionPath => ({
    type: actionTypes.CLEAR_ACTION,
    actionPath,
});

export const runReadOnlyActionThunk = ({
    actionPath,
    params,
    funcs: {
        deleteConfigTransactionsThunk = _deleteConfigTransactionsThunk,
        getDataThunk = _getDataThunk,
        read = _read,
        runAction = _runAction,
    } = {},
} = {}) => (
    dispatch => (
        dispatch(deleteConfigTransactionsThunk()).then(() => {
            const path = actionPath;
            dispatch(runActionStartAction(actionPath));
            return dispatch(read({ path })).then(({ th }) => (
                runAction({ path, th, params })
            )).then((result) => {
                dispatch(runCustomActionCompleteAction({ actionPath, result }));
                dispatch(getDataThunk({ path }));
            }).catch(error => (
                dispatch(runActionErrorAction(actionPath, error))
            ));
        })
    )
);

const packagesReloadActionThunk = ({
    actionPath,
    params,
    funcs: {
        doConfirm = _doConfirm,
        runReadOnlyAction = runReadOnlyActionThunk,
    } = {},
}) => dispatch => (
    dispatch(doConfirm(
        translate(Messages.CONFIGURATIONEDITOR.RELOAD_PACKAGES_MESSAGE),
        translate(Messages.CONFIGURATIONEDITOR.RELOAD_PACKAGES_TITLE),
    )).then((status) => {
        if (status === 0) {
            return Promise.resolve(runActionAbortAction(actionPath));
        }
        return dispatch(runReadOnlyAction({ actionPath, params }));
    })
);

const getApplyTemplateParams = async ({
    th,
    path,
    params = {},
    funcs: {
        query = _query,
        showConfig = _showConfig,
    } = {},
} = {}) => {
    const templateParams = {};
    const config = await showConfig({ th, path });

    const applyTemplate = config['ncs:apply-template'];
    if (applyTemplate) {
        if ('accept-empty-capabilities' in applyTemplate) {
            templateParams['accept-empty-capabilities'] = true;
        }
        if ('suppress-positive-result' in applyTemplate) {
            templateParams['suppress-positive-result'] = true;
        }
        if ('template-name' in applyTemplate) {
            templateParams['template-name'] = applyTemplate['template-name'];
        }
        if (isShowConfigList(applyTemplate.variable)) {
            const selection = ['name', 'value'];
            const result = await query(
                {
                    path: `${path}/variable`,
                    result_as: 'keypath-value',
                    selection,
                    th,
                },
            );
            if (result && Array.isArray(result.results)) {
                templateParams.variable = zipQueryResult(
                    selection,
                    result.results,
                );
            }
        }
    }

    if (params.variable) {
        templateParams.variable = params.variable;
    }

    return templateParams;
};

const applyTemplateThunk = ({
    actionPath,
    keepInputParams,
    params,
    shouldRefresh,
    funcs: {
        deleteTrans = _deleteTrans,
        getApplyTemplateParamsFunc = getApplyTemplateParams,
        getDataThunk = _getDataThunk,
        newTrans = _newTrans,
        runAction = _runAction,
        writeThunk = write,
    } = {},
} = {}) => async (dispatch) => {
    const path = actionPath;
    dispatch(runActionStartAction(actionPath));

    try {
        const { th } = await dispatch(writeThunk());
        const { th: actionTh } = await dispatch(writeThunk({ actionPath }));

        const templateParams = await getApplyTemplateParamsFunc({ path, th: actionTh, params });
        const result = await runAction({ path, th, params: templateParams });

        if (!keepInputParams) {
            await dispatch(deleteTrans(actionTh));
            const { th: newTh } = await dispatch(newTrans({ actionPath }));
            dispatch(runCustomActionCompleteAction({ actionPath, result, oldTh: actionTh, newTh }));
        } else {
            dispatch(runCustomActionCompleteAction({ actionPath, result, oldTh: actionTh }));
        }

        if (shouldRefresh) {
            await dispatch(getDataThunk({ path }));
        }
    } catch (error) {
        dispatch(runActionErrorAction(actionPath, error));
    }
};

const customParamsFactory = params => ({
    executePath,
    actionPath,
    funcs: {
        runAction = _runAction,
        writeThunk = write,
    } = {},
}) => async (dispatch) => {
    dispatch(runActionStartAction(actionPath));

    try {
        const { th } = await dispatch(writeThunk());
        const result = await runAction({ path: executePath, th, params });
        dispatch(runCustomActionCompleteAction({ actionPath, result }));
    } catch (error) {
        dispatch(runActionErrorAction(actionPath, error));
    }
};
const acknowledgeActionThunk = customParamsFactory({ state: 'ack' });
const reDeployDryRunActionThunk = customParamsFactory({ 'dry-run': { outformat: 'cli' } });
const purgeClearedActionThunk = customParamsFactory({ 'alarm-status': 'cleared' });

const customActionThunkFactory = ({ action, actionPath }) => {
    if (action === 're-deploy dry-run') {
        return reDeployDryRunActionThunk;
    }

    if (action === 'acknowledge') {
        return acknowledgeActionThunk;
    }

    if (action === 'purge cleared') {
        return purgeClearedActionThunk;
    }

    if (actionPath === '/ncs:packages/reload') {
        return packagesReloadActionThunk;
    }

    if (/^\/ncs:devices\/device\{[^}]+\}\/apply-template$/.test(actionPath)) {
        return applyTemplateThunk;
    }

    if (/^\/ncs:devices\/device-group\{[^}]+\}\/apply-template$/.test(actionPath)) {
        return applyTemplateThunk;
    }
    return undefined;
};

export const runCustomActionThunk = ({
    action,
    actionPath,
    params,
    executePath,
    funcs: {
        customActionFactory = customActionThunkFactory,
        runAction = _runAction,
        writeThunk = write,
    } = {},
} = {}) => async (dispatch) => {
    const customActionThunk = customActionFactory({ action, actionPath });

    if (customActionThunk) {
        return dispatch(customActionThunk({ action, actionPath, params, executePath }));
    }

    dispatch(runActionStartAction(actionPath));
    try {
        const { th } = await dispatch(writeThunk());
        const result = await runAction({ path: executePath || actionPath, th });
        dispatch(runCustomActionCompleteAction({ actionPath, result }));
    } catch (error) {
        dispatch(runActionErrorAction(actionPath, error));
    }
    return undefined;
};

export const runActionThunk = ({
    actionPath,
    shouldRefresh,
    keepInputParams,
    funcs: {
        customActionFactory = customActionThunkFactory,
        deleteTrans = _deleteTrans,
        getDataThunk = _getDataThunk,
        jsonrpc = jsonrpcSimple,
        newTrans = _newTrans,
        runAction = _runAction,
        writeThunk = write,
    } = {},
} = {}) => async (dispatch) => {
    const path = actionPath;
    const customActionThunk = customActionFactory({ actionPath });

    if (customActionThunk) {
        return dispatch(customActionThunk({
            actionPath,
            keepInputParams,
            shouldRefresh,
        }));
    }

    dispatch(runActionStartAction(actionPath));
    try {
        const { th } = await dispatch(writeThunk({ actionPath }));
        // NOTE(dbildstr): We ignore the validate result here, unless
        // there was an error, which would cause an exception that is
        // caught by the try/catch.
        await jsonrpc('validate_trans', { th });
        const result = await runAction({ path, th });
        if (!keepInputParams) {
            await dispatch(deleteTrans(th));
            const { th: newTh } = await dispatch(newTrans({ actionPath }));
            dispatch(runActionCompleteAction({ actionPath, result, oldTh: th, newTh }));
        } else {
            dispatch(runActionCompleteAction({ actionPath, result, oldTh: th }));
        }

        if (shouldRefresh) {
            await dispatch(getDataThunk({ path }));
        }
    } catch (error) {
        dispatch(runActionErrorAction(actionPath, error));
    }
    return undefined;
};

export const resetActionThunk = ({
    actionPath,
    funcs: {
        deleteTrans = _deleteTrans,
        getTrans = _getTrans,
        newTrans = _newTrans,
    } = {},
} = {}) =>
    async (dispatch) => {
        try {
            dispatch(resetActionStartAction(actionPath));
            const { transactions } = await dispatch(getTrans());
            const oldTh = findLastTransaction(transactions, { actionPath });
            if (oldTh) {
                await dispatch(deleteTrans(oldTh));
            }
            const { th: newTh } = await dispatch(newTrans({ actionPath }));
            dispatch(resetActionCompleteAction({ actionPath, oldTh, newTh }));
        } catch (error) {
            dispatch(resetActionErrorAction(actionPath, error));
        }
    };

export const loadActionAction = ({ actionPath }) => ({
    type: actionTypes.LOAD_ACTION_ACTION,
    actionPath,
});

export const clearYangActionStates = ({
    paths = [],
} = {}) => (dispatch, getState) => {
    const state = getState();
    paths.forEach((path) => {
        const actionPaths = selectYangActionPathsByParent(state, path);
        actionPaths.forEach(actionPath => dispatch(clearActionAction(actionPath)));
    });
    return Promise.resolve();
};

export {
    acknowledgeActionThunk as _acknowledgeActionThunk,
    applyTemplateThunk as _applyTemplateThunk,
    customActionThunkFactory as _customActionThunkFactory,
    customParamsFactory as _customParamsFactory,
    getApplyTemplateParams as _getApplyTemplateParams,
    packagesReloadActionThunk as _packagesReloadActionThunk,
    purgeClearedActionThunk as _purgeClearedActionThunk,
    reDeployDryRunActionThunk as _reDeployDryRunActionThunk,
};
