import * as actionTypes from './actionTypes';
import { formatPath } from '@webui-one/core/utils/path';
import { zipWithProp } from '@webui-one/core/utils/array';
import { isNonEmptyString, removePrefix } from '@webui-one/core/utils/string';
import { WEBUI_ROOT } from '@webui-one/core/defaultApplications';

const zipApplications = results => zipWithProp(results, 'id');

const reg = /^https?:\/\/|\/custom\//i;
export const formatAppHref = href => (
    isNonEmptyString(href) && !reg.test(href)
        ? formatPath(removePrefix(href, WEBUI_ROOT))
        : href
);

const updateAppHref = apps => apps.map((app) => {
    const href = formatAppHref(app.href);
    if (href === app.href) {
        return app;
    }
    return {
        ...app,
        href,
    };
});

export const getApplicationsStartAction = () => ({
    type: actionTypes.APPLICATIONS_GET_START,
});

export const getApplicationsAction = (selection, results) => ({
    isLoading: false,
    type: actionTypes.APPLICATIONS_GET,
    applications: zipApplications(updateAppHref(results)),
});

export const getApplicationsAsyncAction = () => ({
    type: actionTypes.APPLICATIONS_GET_ASYNC,
});

export {
    updateAppHref as _updateAppHref,
    zipApplications as _zipApplications,
};
