import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from '@webui-one/router';
import { WEBUI_ROOT } from '@webui-one/core/defaultApplications';
import { getRootPath } from '@webui-one/core/utils/browser';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import {
    initTracing,
    resetTracing,
} from '@webui-one/core/utils/tracing';
import { getApplicationName } from 'modules/application/utils';
import { read } from 'modules/transactions/actions';
import { selectReadTh } from 'modules/transactions/selectors';
import * as flagTypes from 'modules/flags/flagTypes';
import { flagIsSet } from '@webui-one/core/flags';
import { getSystemSetting } from 'modules/systemSetting/actions';
import { getLinkWithPath as configurationEditorGetLinkWithPath } from 'ConfigurationEditor/utils';
import { selectPathname } from 'modules/routing/selectors';
import Application from './Application';


const setDocumentTitle = (applicationName, path) => {
    let title = '';
    if (applicationName) {
        title = `${applicationName}`;
    }
    if (path && applicationName && applicationName === 'Configuration editor') {
        const configPathWithoutRoot = path.replace(configurationEditorGetLinkWithPath(''), '');
        const shortPath = configPathWithoutRoot.split('/').slice(0, 4).join('/');
        title += ` ${shortPath}`;
    }
    document.title = title;
};

const predicate = () => flagIsSet(flagTypes.TRACING_ENABLE, 'true');

// TODO(dbildstr): should this use an ApplicationContext instead of the Redux state?
class ApplicationContainer extends React.Component {
    componentDidMount() {
        const {
            applicationName,
            getWebuiRootPath,
            initUserTiming,
            onGetSystemSetting,
            path,
            version,
            th,
            onGetReadTrans,
            router,
        } = this.props;

        const webuiRootPath = getWebuiRootPath();

        if (webuiRootPath !== WEBUI_ROOT
            || (isNonEmptyString(router?.location?.pathname)
                && router.location.pathname === WEBUI_ROOT
            )
        ) {
            router.replace(`${WEBUI_ROOT}/`);
        }

        initUserTiming(predicate);
        if (!version) {
            onGetSystemSetting();
        }
        if (!th) {
            onGetReadTrans();
        }
        setDocumentTitle(applicationName, path);
    }

    componentDidUpdate() {
        const { applicationName, path } = this.props;

        setDocumentTitle(applicationName, path);
    }

    componentWillUnmount() {
        const { resetUserTiming } = this.props;
        resetUserTiming();
    }

    render() {
        const { children } = this.props;
        return (
            <Application>
                {children}
            </Application>
        );
    }
}

ApplicationContainer.propTypes = {
    applicationName: PropTypes.string,
    children: PropTypes.node,
    getWebuiRootPath: PropTypes.func,
    initUserTiming: PropTypes.func,
    onGetSystemSetting: PropTypes.func,
    path: PropTypes.string,
    resetUserTiming: PropTypes.func,
    router: PropTypes.shape({
        location: PropTypes.shape({
            pathname: PropTypes.string,
        }),
        replace: PropTypes.func,
    }),
    version: PropTypes.string.isRequired,
    th: PropTypes.number,
    onGetReadTrans: PropTypes.func,
};

ApplicationContainer.defaultProps = {
    version: '',
    path: '',
    getWebuiRootPath: getRootPath,
    initUserTiming: initTracing,
    onGetSystemSetting: () => (null),
    router: {
        location: {},
        replace: () => undefined,
    },
    resetUserTiming: resetTracing,
};

const mapStateToProps = (state, ownProps) => ({
    applicationName: getApplicationName(state, ownProps),
    path: selectPathname(state),
    th: selectReadTh(state),
});

const mapDispatchToProps = dispatch => ({
    onGetSystemSetting() {
        dispatch(getSystemSetting());
    },
    onGetReadTrans() {
        dispatch(read());
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(withRouter(ApplicationContainer));

export {
    ApplicationContainer as _ApplicationContainer,
    mapDispatchToProps as _mapDispatchToProps,
    predicate as _predicate,
};
