import React from 'react';
import PropTypes from 'prop-types';
import { isString } from '@webui-one/core/utils/string';
import {
    LocationPropType,
    MatchPropType,
    RouterPropType,
} from './router-prop-types';
import withRouter from './withRouter';
import { matchPath } from '../router-utils/match';
import { formatPattern } from '../router-utils/patternUtils';
import { createLocation, locationsAreEqual } from '../router-utils/routerUtils';

class Redirect extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    static getDerivedStateFromProps(props) {
        const { computedMatch, to } = props;

        if (!computedMatch) {
            return { location: createLocation(to) };
        }

        if (isString(to)) {
            return { location: createLocation(to, computedMatch.params) };
        }

        return {
            location: createLocation({
                ...to,
                pathname: formatPattern(to.pathname, computedMatch.params),
            }),
        };
    }

    componentDidMount() {
        const { push, router } = this.props;
        const { location } = this.state;
        const method = push ? router.push : router.replace;
        if (this.isMatch()) {
            method(location);
        }
    }

    componentDidUpdate(prevProps) {
        const { push, router } = this.props;
        const { location } = this.state;
        const method = push ? router.push : router.replace;
        const prevLocation = createLocation(prevProps.to);
        if (this.isMatch() && !locationsAreEqual(
            prevLocation,
            {
                ...location,
                key: prevLocation.key,
            },
        )) {
            method(location);
        }
    }

    isMatch() {
        const {
            computedMatch,
            exact,
            from,
            location: routerLocation,
        } = this.props;
        const routePath = from || '';
        return computedMatch || matchPath(routerLocation, routePath, exact);
    }

    render() {
        return null;
    }
}

Redirect.propTypes = {
    computedMatch: MatchPropType,
    exact: PropTypes.bool,
    from: PropTypes.string,
    location: LocationPropType,
    push: PropTypes.bool,
    router: RouterPropType,
    to: PropTypes.oneOfType([
        PropTypes.string,
        LocationPropType,
    ]),
};

export default withRouter(Redirect);

export {
    Redirect as _RedirectComponent,
};
