import { formatPath } from '@webui-one/core/utils/path';

export default {
    path: () => formatPath('DeviceManager'),
    getComponent(nextState, cb) {
        import(/* webpackChunkName: "DeviceManager" */'./DeviceManager').then((DeviceManager) => {
            cb(null, DeviceManager.default);
        }, () => undefined)
            .catch(() => undefined);
    },
};
