import { isEmpty, isNonEmptyArray, last } from '@webui-one/core/utils/array';
import { isNonEmptyString, isString } from '@webui-one/core/utils/string';
import { warning as warningFunc } from '@webui-one/core/utils/functional';
import { isDeepEqual, isObject } from '@webui-one/core/utils/object';

export const assignRouterState = (router, state) => {
    const nextRouter = router;
    nextRouter.location = state.location;
    nextRouter.params = state.params;
    return nextRouter;
};

export const createRouterObject = (history, state) => {
    const router = {
        ...history,
    };
    return assignRouterState(router, state);
};

export const extractPath = (string) => {
    const match = string.match(/^(https?:)?\/\/[^/]*/);
    return match == null ? string : string.substring(match[0].length);
};

const isAbsolute = pathname => isString(pathname) && pathname.startsWith('/');

const isTrailingSlash = part => (
    part === '.' || part === '..' || part === ''
);

const splitByDelim = (str, delim) => (
    (str && str.split(delim)) || []
);

function parseFrom(toParts, toIsAbsolute, from) {
    const fromParts = splitByDelim(from, '/');
    if (toIsAbsolute) {
        return toParts.slice();
    }
    if (toParts.length) {
        return [
            ...fromParts.slice(0, fromParts.length - 1),
            ...toParts,
        ];
    }
    return fromParts;
}

function pathnameFromParts(fromParts, mustEndAbsolute) {
    const parents = [];
    let pathnameParts = fromParts.reduceRight((parts, part) => {
        if (part === '.') {
            return parts;
        }
        if (part === '..') {
            parents.push('..');
            return parts;
        }
        if (isNonEmptyArray(parents)) {
            parents.pop();
            return parts;
        }
        parts.push(part);
        return parts;
    }, []);

    if (!mustEndAbsolute) {
        pathnameParts = parents.reduce((parts, part) => {
            parts.push(part);
            return parts;
        }, pathnameParts);
    }

    const lastPart = last(pathnameParts);
    if (
        mustEndAbsolute
        && lastPart !== ''
        && (!lastPart || !isAbsolute(lastPart))
    ) {
        pathnameParts.push('');
    }

    return pathnameParts.reverse().join('/');
}

export function resolvePathname(to, from) {
    const fromPath = from !== undefined ? from : '';

    const toParts = splitByDelim(to, '/');

    const isToAbsolute = to && isAbsolute(to);
    const isFromAbsolute = fromPath && isAbsolute(fromPath);
    const mustEndAbsolute = isToAbsolute || isFromAbsolute;
    const fromParts = parseFrom(toParts, isToAbsolute, fromPath);

    if (isEmpty(fromParts)) {
        return '/';
    }

    const hasTrailingSlash = isTrailingSlash(last(fromParts));

    const resolvedPathname = pathnameFromParts(fromParts, mustEndAbsolute);

    return hasTrailingSlash && !resolvedPathname.endsWith('/')
        ? `${resolvedPathname}/`
        : resolvedPathname;
}

export function parsePath(path, { warning = warningFunc } = {}) {
    let pathname = extractPath(path);
    let search = '';
    let hash = '';

    warning(
        path === pathname,
        'A path must be pathname + search + hash only, not a full URL like "%s"',
        path,
    );

    const hashIndex = pathname.indexOf('#');
    if (hashIndex !== -1) {
        hash = pathname.substring(hashIndex);
        pathname = pathname.substring(0, hashIndex);
    }

    const searchIndex = pathname.indexOf('?');
    if (searchIndex !== -1) {
        search = pathname.substring(searchIndex);
        pathname = pathname.substring(0, searchIndex);
    }

    if (pathname === '') {
        pathname = '/';
    }

    return {
        pathname,
        search,
        hash,
    };
}

export const locationFromPath = (
    path,
    state,
    { pathParser = parsePath } = {},
) => {
    if (isString(path)) {
        return {
            ...pathParser(path),
            state,
        };
    }

    const location = { ...path };

    if (!location.pathname) {
        location.pathname = '';
    }

    if (isNonEmptyString(location.search)) {
        if (!location.search.startsWith('?')) {
            location.search = `?${location.search}`;
        }
    } else {
        location.search = '';
    }

    if (isNonEmptyString(location.hash)) {
        if (!location.hash.startsWith('#')) {
            location.hash = `#${location.hash}`;
        }
    } else {
        location.hash = '';
    }

    if (state !== undefined && location.state === undefined) {
        location.state = state;
    }

    return location;
};

export const createLocation = (
    path,
    state,
    key,
    currentLocation,
    { getLocationFromPath = locationFromPath } = {},
) => {
    const location = getLocationFromPath(path, state);

    if (key) {
        location.key = key;
    }

    if (currentLocation) {
        if (!location.pathname) {
            location.pathname = currentLocation.pathname;
        } else if (!location.pathname.startsWith('/')) {
            location.pathname = resolvePathname(
                location.pathname,
                currentLocation.pathname,
            );
        }
    } else if (!location.pathname) {
        location.pathname = '/';
    }

    return location;
};

export const locationsAreEqual = (a, b) => {
    if (a === b) return true;
    if (!isObject(a) || !isObject(b)) return false;

    return a.pathname === b.pathname
    && a.search === b.search
    && a.hash === b.hash
    && a.key === b.key
    && isDeepEqual(a.state, b.state);
};
