export const sequence = promises => (
    promises.reduce((prev, cur) => (
        prev.then((acc) => {
            let promise = cur;
            if (typeof cur === 'function') {
                promise = cur();
            }
            return promise.then(res => acc.concat(res));
        })
    ), Promise.resolve([]))
);

export const isPromise = value => (
    value && typeof value.then === 'function'
);
