export const RE_DEVICES_DEVICE = new RegExp('^/ncs:devices/device$');
export const RE_DEVICES_DEVICE_CONFIG = new RegExp('^/ncs:devices/device{[^}]*}/config$');
export const RE_DEVICES_DEVICE_CONFIG_CHILD =
    new RegExp('(^/ncs:devices/device{[^}]*})/(config/([^:]+?):.*)$');
export const RE_DEVICES_DEVICE_LIST_ENTRY = new RegExp('^/ncs:devices/device{([^}]*)}$');
export const RE_DEVICES_DEVICE_LIST_ENTRY_OR_CHILD = (
    new RegExp('^(/ncs:devices/device{([^}]*)}).*$')
);
