
import { omitKeys } from '@webui-one/core/utils/object';

import * as nodeActionTypes from 'modules/nodes/actionTypes';
import * as actionTypes from './actionTypes';
import { deviceListPath } from './paths';
import { labelTypes } from './selectors';

const getKeyFromLabel = (label) => {
    if (label.type === labelTypes.SEARCH) return labelTypes.SEARCH;
    return label.type + label.name;
};

export const labels = (state = {}, action = {}) => {
    const { label } = action;
    const labelKey = getKeyFromLabel(label);

    switch (action.type) {
    case actionTypes.LABEL_ADD: {
        return {
            ...state,
            [labelKey]: label,
        };
    }
    case actionTypes.LABEL_REMOVE: {
        return omitKeys(state, [labelKey]);
    }
    default:
        return state;
    }
};

export const reduceExpanded = (state = {}, action = {}) => {
    if (action.type === actionTypes.SET_EXPANDED_ROW) {
        return {
            ...state,
            [action.path]: {
                isExpanded: action.isExpanded,
                column: action.column,
            },
        };
    }

    return state;
};

const initialState = {
    sortOrder: {
        columnKey: 'name',
        isDesc: false,
    },
    labels: {},
    devices: {},
    modules: {},
    lastDeleteDeviceTimestamp: undefined,
    isGettingDevices: false,
};

const handleDelete = (state, action) => {
    if (!(action.path in state.devices)) {
        return state;
    }
    return {
        ...state,
        devices: {
            ...omitKeys(state.devices, [action.path]),
        },
    };
};

const handleDeletePaths = (state, action) => (
    action.paths.reduce((acc, path) => {
        if (!(path in state.devices)) {
            return acc;
        }
        acc.devices = {
            ...omitKeys(state.devices, [path]),
        };
        return acc;
    }, { ...state })
);

function handleDeleteNodesComplete(state, action) {
    if (action.path !== deviceListPath) {
        return state;
    }

    return {
        ...state,
        lastDeleteDeviceTimestamp: Date.now(),
    };
}

export default function devices(state = initialState, action = {}) {
    switch (action.type) {
    case actionTypes.DEVICES_SELECT:
        return {
            ...state,
            devices: action.deviceNames.reduce((acc, deviceName) => ({
                ...acc,
                [deviceName]: {
                    ...state[deviceName],
                    isSelected: action.isSelected,
                },
            }), state.devices),
        };
    case nodeActionTypes.DELETE:
        return handleDelete(state, action);
    case nodeActionTypes.DELETE_PATHS:
        return handleDeletePaths(state, action);
    case actionTypes.LABEL_ADD:
    case actionTypes.LABEL_REMOVE: {
        return {
            ...state,
            labels: labels(state.labels, action),
        };
    }
    case actionTypes.LOAD_MODULES_START:
        return {
            ...state,
            modules: {
                ...state.modules,
                [action.path]: { isLoading: true },
            },
        };
    case actionTypes.LOAD_MODULES_COMPLETE:
        return {
            ...state,
            modules: {
                ...state.modules,
                [action.path]: {
                    isLoading: false,
                    modules: action.result,
                },
            },
        };
    case actionTypes.LOAD_MODULES_ERROR:
        return {
            ...state,
            modules: {
                ...state.modules,
                [action.path]: {
                    isLoading: false,
                    error: action.error,
                },
            },
        };
    case actionTypes.REMOVE_DEVICES_FROM_GROUP_START:
    case actionTypes.ADD_DEVICES_TO_GROUP_START:
        return {
            ...state,
            deviceGroupChangeIsPending: true,
        };
    case actionTypes.REMOVE_DEVICES_FROM_GROUP_COMPLETE:
    case actionTypes.REMOVE_DEVICES_FROM_GROUP_ERROR:
    case actionTypes.ADD_DEVICES_TO_GROUP_COMPLETE:
    case actionTypes.ADD_DEVICES_TO_GROUP_ERROR:
        return {
            ...state,
            deviceGroupChangeIsPending: false,
        };
    case nodeActionTypes.DELETE_COMPLETE:
        return handleDeleteNodesComplete(state, action);
    case actionTypes.SET_EXPANDED_ROW:
        return {
            ...state,
            expandedRows: reduceExpanded(state.expandedRows, action),
        };
    case actionTypes.GETTING_DEVICES_START:
        return {
            ...state,
            isGettingDevices: true,
        };
    case actionTypes.GETTING_DEVICES_COMPLETE:
        return {
            ...state,
            isGettingDevices: false,
        };
    case actionTypes.LOAD_DEVICE_GROUPS_START:
        return {
            ...state,
            loadingGroups: true,
        };
    case actionTypes.LOAD_DEVICE_GROUPS_ERROR:
        return {
            ...state,
            deviceGroupsError: action.error,
            loadingGroups: false,
        };
    case actionTypes.LOAD_DEVICE_GROUPS_COMPLETE:
        return {
            ...state,
            deviceGroupsWithDevices: action.groups,
            loadingGroups: false,
        };
    case actionTypes.LOAD_DEVICE_GROUPS_FOR_DEVICES_START:
        return {
            ...state,
            error: undefined,
            groupsForDevices: [],
            loadingGroupsForDevices: true,
        };
    case actionTypes.LOAD_DEVICE_GROUPS_FOR_DEVICES_ERROR:
        return {
            ...state,
            deviceGroupsForDevicesError: action.error,
            loadingGroupsForDevices: false,
        };
    case actionTypes.LOAD_DEVICE_GROUPS_FOR_DEVICES_COMPLETE:
        return {
            ...state,
            groupsForDevices: action.groups,
            loadingGroupsForDevices: false,
        };
    default:
        return state;
    }
}
