import * as KP from '@webui-one/core/keypath/kp-parser';
import {
    selectSchema as selectSchema2,
    selectSchemaFormats,
} from 'modules/schemas2/selectors';

export const selectSchema = (state, path) => {
    const selectOptions = {
        selectSchemaFormat: selectSchemaFormats.SELECT_SCHEMA,
    };
    return selectSchema2(state, { path, selectOptions });
};

export const toKeypath = (parentPath, childPath) => {
    if (KP.isListKeyPath(childPath)) return childPath;

    const parentSchemaPath = KP.pathWithoutKeys(parentPath);
    return childPath.replace(parentSchemaPath, parentPath);
};
