import { getHistoryState } from '@webui-one/core/utils/browser';
import {
    createPathFromLocation,
} from '@webui-one/core/utils/path';
import { createRandomString } from '@webui-one/core/utils/random';
import { createLocation } from './routerUtils';

export const createListenerManager = () => {
    let listeners = [];
    const manager = {};
    manager.length = listeners.length;

    const add = (listener) => {
        listeners.push(listener);
        manager.length = listeners.length;

        return () => {
            listeners = listeners.filter(l => l !== listener);
            manager.length = listeners.length;
        };
    };

    const notify = (...args) => {
        listeners.forEach(listener => listener(...args));
    };

    manager.add = add;
    manager.notify = notify;
    return manager;
};

export const isExtraneousPopstateEvent = (event, userAgent) => (
    event.state === undefined && userAgent.indexOf('CriOS') === -1
);

const createBrowserHistory = ({
    funcs: {
        generateKey = createRandomString,
    } = {},
} = {}) => {
    let history;
    const keyLength = 6;
    const globalHistory = window.history;
    const manager = createListenerManager();

    const getDOMLocation = (historyState) => {
        const { key, state } = historyState || {};
        const { pathname, search, hash } = window.location;

        const path = pathname + search + hash;

        return createLocation(path, state, key);
    };

    const initialLocation = getDOMLocation(getHistoryState());
    let allKeys = [initialLocation.key];

    const createKey = () => generateKey(keyLength);

    const createHref = location => createPathFromLocation(location);

    const getCurrentLocation = () => window.location;

    const go = (n) => {
        globalHistory.go(n);
    };

    const goBack = () => {
        go(-1);
    };

    const goForward = () => {
        go(1);
    };

    const setState = (nextState) => {
        Object.assign(history, nextState);
        history.length = globalHistory.length;
        manager.notify(history.location, history.action);
    };

    const handlePopState = (evt) => {
        if (isExtraneousPopstateEvent(evt, navigator.userAgent)) {
            return;
        }
        const location = getDOMLocation(evt.state);
        setState({ action: 'POP', location });
    };

    const listen = (listener) => {
        const unlisten = manager.add(listener);
        if (manager.length === 1) {
            window.addEventListener('popstate', handlePopState);
        }
        return () => {
            unlisten();
            if (manager.length === 0) {
                window.removeEventListener('popstate', handlePopState);
            }
        };
    };

    const push = (path, newState) => {
        const action = 'PUSH';
        const location = createLocation(path, newState, createKey(), history.location);
        const href = createHref(location);
        const { key, state } = location;
        globalHistory.pushState({ key, state }, null, href);
        const prevIndex = allKeys.indexOf(history.location.key);
        const nextKeys = allKeys.slice(
            0,
            prevIndex === -1 ? 0 : prevIndex + 1,
        );
        nextKeys.push(location.key);
        allKeys = nextKeys;
        setState({ action, location });
    };
    const replace = (path, newState) => {
        const action = 'REPLACE';
        const location = createLocation(path, newState, createKey(), history.location);
        const href = createHref(location);
        const { key, state } = location;
        globalHistory.replaceState({ key, state }, null, href);
        const prevIndex = allKeys.indexOf(history.location.key);
        if (prevIndex !== -1) allKeys[prevIndex] = location.key;
        setState({ action, location });
    };

    history = {
        action: 'POP',
        createHref,
        getCurrentLocation,
        go: globalHistory.go,
        goBack,
        goForward,
        length: globalHistory.length,
        listen,
        location: initialLocation,
        push,
        replace,
    };

    return history;
};

export default createBrowserHistory;
