import * as actionTypes from './actionTypes';

export const includeSubfoldersAction = value => ({ type: actionTypes.INCLUDE_SUBFOLDERS, value });
export const showActionsAction = value => ({ type: actionTypes.SHOW_ACTIONS, value });
export const showAllAction = value => ({ type: actionTypes.SHOW_ALL, value });
export const showEmptyAction = value => ({ type: actionTypes.SHOW_EMPTY, value });
export const showOperdataAction = value => ({ type: actionTypes.SHOW_OPERDATA, value });
export const setEditModeAction = value => ({ type: actionTypes.SET_EDIT_MODE, value });
export const getModelsAndPackagesAction = () => ({
    type: actionTypes.GET_MODELS_AND_PACKAGES,
});
