import { LOCATION_CHANGE } from '@webui-one/router';
import { getApplicationKey } from '@webui-one/core/utils/path';

const initialState = {
    currentApp: '',
    currentUrl: '',
    previousApp: '',
    previousUrl: '',
};

const isSameApp = (first, second) => {
    if (!(first && second)) return false;
    return (first === second);
};

export default function backNav(state = initialState, action = {}) {
    if (action.type === LOCATION_CHANGE) {
        const nextApp = getApplicationKey(action.location.pathname);
        if (isSameApp(nextApp, state.currentApp)) {
            return {
                ...state,
                currentUrl: action.location.pathname,
                previousUrl: state.currentUrl,
            };
        }
        return {
            ...state,
            currentApp: nextApp,
            previousApp: state.currentApp,
            currentUrl: action.location.pathname,
            previousUrl: state.currentUrl,
        };
    }

    return state;
}

export function selectPreviousUrl(state) {
    if (state && state.backNav && state.backNav.previousUrl !== undefined) {
        return state.backNav.previousUrl;
    }
    return '';
}
