import {
    getSchemas as _getSchemas,
    showConfigJson2 as _showConfig,
} from '@webui-one/core/jsonrpc';

import * as S from '@webui-one/core/schema';

import { abortWrapper } from '@webui-one/core/utils/async';
import { isObject } from '@webui-one/core/utils/object';
import { uniq, zipObject } from '@webui-one/core/utils/array';
import {
    isNonEmptyString,
    lstrip,
} from '@webui-one/core/utils/string';
import { pathToObjects } from '@webui-one/core/keypath/kp-parser';
import {
    _flattenSchema as flattenSchema,
    toSchemaPath,
} from 'modules/schemas/reducer';
import {
    selectSchema as _selectSchema,
} from 'modules/schemas/selectors';
import {
    loadStatus,
} from 'modules/schemas/utils';
import {
    getTypesFromResponses as _getTypesFromResponses,
    getSchemasFromResponses as _getSchemasFromResponses,
} from 'modules/schemas2/actions';
import {
    write as _write,
    read as _read,
} from 'modules/transactions/actions';
import {
    selectActionPath as _selectActionPath,
} from 'modules/transactions/selectors';
import * as actionTypes from './actionTypes';
import {
    setCaseCompleteAction,
} from 'modules/nodes/actions';

export const getDataCompleteAction = ({ path, data, schemas, schemas2, types }) => ({
    type: actionTypes.GET_DATA_COMPLETE,
    path,
    data,
    schemas,
    schemas2,
    types,
});

export const getDataStartAction = path => ({
    type: actionTypes.GET_DATA_START,
    path,
});

export const getDataErrorAction = (path, error) => ({
    type: actionTypes.GET_DATA_ERROR,
    path,
    error,
});

export const reloadCaseDataStartAction = path => ({
    type: actionTypes.RELOAD_CASE_DATA_START,
    path,
});

export const reloadCaseDataCompleteAction = (path, data) => ({
    type: actionTypes.RELOAD_CASE_DATA_COMPLETE,
    path,
    data,
});

export const reloadCaseDataErrorAction = (path, error) => ({
    type: actionTypes.RELOAD_CASE_DATA_ERROR,
    path,
    error,
});

export const abortErrorAction = (error, aborter, info) => ({
    aborter,
    error,
    info,
    type: actionTypes.ABORT_ERROR,
});

const isList = value => (
    Array.isArray(value) && value.length > 1 && value[0] === null
);

const isLeafList = value => (
    Array.isArray(value) && value.length > 0 && value[0] !== null
);

const isEmpty = value => (
    Array.isArray(value) && value.length === 1 && value[0] === null
);

const processData = (data, path = '') => Object.keys(data).reduce((acc, cur) => {
    const value = data[cur];
    const currentPath = `${path}/${cur}`;
    if (isObject(value)) {
        return {
            ...acc,
            [currentPath]: {},
            ...processData(value, currentPath),
        };
    }
    if (isList(value)) {
        acc[currentPath] = {
            totalNumberOfResults: value[1],
        };
        return acc;
    }

    if (isEmpty(value)) {
        acc[currentPath] = {
            exists: true,
            value: true,
        };
        return acc;
    }

    if (isLeafList(value)) {
        acc[currentPath] = {
            totalNumberOfResults: value.length,
            rows: value.map(v => ({ 0: v })),
        };
        return acc;
    }
    acc[currentPath] = { value: `${value}` };

    return acc;
}, {});

const reduceSchemas = paths => (acc, schemaResult, idx) => {
    if (schemaResult.error) {
        acc[toSchemaPath(paths[idx])] = {
            data: {},
            error: schemaResult.error,
            loadStatus: loadStatus.ERROR,
            meta: {},
        };
        return acc;
    }
    return {
        ...acc,
        ...flattenSchema(schemaResult.result.meta.keypath, schemaResult.result, true),
    };
};

const processSchemas = ({
    schemaResponses,
    paths,
    funcs: {
        getSchemasFromResponses = _getSchemasFromResponses,
        getTypesFromResponses = _getTypesFromResponses,
    } = {},
} = {}) => {
    const responses = zipObject(paths, schemaResponses);
    const schemas2 = getSchemasFromResponses(responses);
    const types = getTypesFromResponses(schemaResponses);
    return { schemas2, types };
};

const filterLoadedPaths = processedData => uniq(Object.keys(processedData));

// That is levels 1, empty and not empty
// Fetch schema for current path and lists
// This is to fetch the lists columns (leafs)
const noSubfolders = (path, processedData) => {
    const listsPaths = Object.keys(processedData)
        .filter(k => processedData[k].totalNumberOfResults > 0)
        .filter(k => k.indexOf('/', path.length + 1) === -1);

    return uniq([path, ...listsPaths]);
};
// That is multiple levels 1, do not show empty
// Fetch schema (levels 1) for current path and all descendant containers
// and lists which have data
const subfoldersWithConfig = (path, processedData) => {
    const pathsWithData = filterLoadedPaths(processedData)
        .filter(k => processedData[k].value === undefined);
    return uniq([path, ...pathsWithData]);
};

// includeSubfolders and showEmpty == true
// That is levels -1 stop on list
// Fetch schema for current stop_on_list = true (-1)
const subfoldersWithoutConfig = path => [path];

export const getPaths = (path, processedData, includeSubfolders, showEmpty) => {
    if (includeSubfolders === false) {
        return noSubfolders(path, processedData);
    }
    if (includeSubfolders === true && showEmpty === false) {
        return subfoldersWithConfig(path, processedData);
    }
    return subfoldersWithoutConfig(path);
};

function getParentPaths(path) {
    const tokens = pathToObjects(path);
    return tokens.reduce((toks, tok) => {
        if (tok.path !== path && tok.listEntry !== true) {
            toks.unshift(tok.path);
        }
        return toks;
    }, []);
}

// eslint-disable-next-line no-underscore-dangle
export const _getConfig = async ({
    path = undefined,
    showOperdata = false,
    th,
    signal,
    funcs: {
        showConfig = _showConfig,
    } = {},
} = {}) => {
    const result = await showConfig({ th, path, withOper: showOperdata, signal });
    const values = Object.values(result);
    if (values.length === 0) {
        return {};
    }
    return {
        [lstrip(path, '/')]: values[0],
    };
};

// eslint-disable-next-line no-underscore-dangle
export const _getProcessedData = async ({
    th,
    path,
    showOperdata,
    signal,
    funcs: {
        getConfig = _getConfig,
        processDataFunc = processData,
    } = {},
} = {}) => {
    const result = await getConfig({ th, path, showOperdata, signal });
    return processDataFunc(result);
};


// eslint-disable-next-line no-underscore-dangle
export const _getProcessedSchemas = async ({
    th,
    path,
    paths,
    includeSubfolders,
    showEmpty,
    signal,
    funcs: {
        getSchemas = _getSchemas,
        processSchemasFunc = processSchemas,
    } = {},
} = {}) => {
    const parentPaths = getParentPaths(path);
    let schemaResponses = await getSchemas({
        th,
        additionalPaths: parentPaths,
        paths,
        path,
        includeSubfolders,
        showEmpty,
        signal,
    });

    const errors = [];
    const parentSchemas = schemaResponses.slice(schemaResponses.length - parentPaths.length);
    schemaResponses = schemaResponses.slice(0, schemaResponses.length - parentPaths.length);
    schemaResponses = schemaResponses.filter((sr, ix) => {
        if (sr.error && sr.error.type !== 'data.not_found') {
            errors.push(sr.error);
            paths.splice(ix, 1);
            return false;
        }
        return true;
    });

    const processedSchemas = processSchemasFunc({ schemaResponses, paths });
    return { parentSchemas, processedSchemas, errors };
};

const getDataAndSchemas = async ({
    th,
    path = undefined,
    includeSubfolders = false,
    showOperdata = false,
    showEmpty = false,
    signal,
    funcs: {
        getProcessedData = _getProcessedData,
        getProcessedSchemas = _getProcessedSchemas,
    } = {},
} = {}) => {
    const processedData = await getProcessedData({ th, path, showOperdata, signal });
    const paths = getPaths(path, processedData, includeSubfolders, showEmpty);
    const { parentSchemas, processedSchemas, errors } = await getProcessedSchemas({
        th,
        path,
        paths,
        includeSubfolders,
        showEmpty,
        signal,
    });
    return { parentSchemas, processedSchemas, processedData, errors };
};

async function getActionPath({
    getState,
    path,
    parentSchemas,
    processedSchemas,
    funcs: {
        selectActionPath = _selectActionPath,
        selectSchema = _selectSchema,
    } = {},
} = {}) {
    const actionPath = selectActionPath(getState(), path);
    if (isNonEmptyString(actionPath)) {
        return actionPath;
    }

    const state = { ...processedSchemas };
    const schema = selectSchema(state, path);

    if (S.isAction(schema)) {
        return path;
    }

    const parentPaths = getParentPaths(path);
    const schemaResponses = parentSchemas;

    return parentPaths.find((schemaPath, ix) => {
        const { result: schemaResult } = schemaResponses[ix];
        return S.isAction(schemaResult) ? schemaPath : undefined;
    });
}

export const getDataThunk = ({
    path = undefined,
    includeSubfolders = false,
    showOperdata = false,
    showEmpty = false,
    funcs: {
        abortWrapperFunc = abortWrapper,
        getActionPathFunc = getActionPath,
        getDataAndSchemasFunc = getDataAndSchemas,
        selectActionPath = _selectActionPath,
        writeThunk = _write,
        readThunk = _read,
    } = {},
} = {}) => abortWrapperFunc(
    async (aborter, signal, dispatch, getState) => {
        try {
            dispatch(getDataStartAction(path));
            let actionPath = selectActionPath(getState(), path);
            let parentSchemas;
            let processedData;
            let processedSchemas;
            let errors;
            if (isNonEmptyString(actionPath)) {
                const { th: actionTh } = await writeThunk({
                    actionPath,
                    signal,
                })(dispatch, getState);
                ({
                    processedData,
                    processedSchemas,
                    errors,
                } = await getDataAndSchemasFunc({
                    th: actionTh,
                    path,
                    includeSubfolders: true,
                    showOperdata: false,
                    showEmpty: true,
                    signal,
                }));

                if (errors) {
                    errors.forEach((error) => {
                        dispatch(getDataErrorAction(path, error));
                    });
                }
            } else {
                const { th } = await readThunk({ path, signal })(dispatch, getState);
                ({
                    parentSchemas,
                    processedData,
                    processedSchemas,
                    errors,
                } = await getDataAndSchemasFunc({
                    th,
                    path,
                    includeSubfolders,
                    showOperdata,
                    showEmpty,
                    signal,
                }));

                if (errors) {
                    errors.forEach((error) => {
                        dispatch(getDataErrorAction(path, error));
                    });
                }
                actionPath = await getActionPathFunc({
                    getState,
                    path,
                    processedSchemas,
                    parentSchemas,
                });
                if (actionPath) {
                    const { th: actionTh } = await writeThunk({
                        actionPath,
                        signal,
                    })(dispatch, getState);
                    ({
                        processedData,
                        processedSchemas,
                        errors,
                    } = await getDataAndSchemasFunc({
                        th: actionTh,
                        path,
                        includeSubfolders: true,
                        showOperdata: false,
                        showEmpty: true,
                        signal,
                    }));

                    if (errors) {
                        errors.forEach((error) => {
                            dispatch(getDataErrorAction(path, error));
                        });
                    }
                }
            }

            return dispatch(
                getDataCompleteAction({
                    path,
                    data: processedData,
                    ...processedSchemas,
                }),
            );
        } catch (error) {
            if (aborter && aborter.aborted()) {
                return dispatch(abortErrorAction(
                    error,
                    aborter,
                    {
                        path,
                        includeSubfolders,
                        showOperdata,
                        showEmpty,
                    },
                ));
            }
            return dispatch(getDataErrorAction(path, error));
        }
    },
);

export const reloadCaseDataThunk = ({
    path = undefined,
    parentNodepath = undefined,
    showOperdata = false,
    caseValue = undefined,
    funcs: {
        getConfig = _getConfig,
        readThunk = _read,
    } = {},
} = {}) => (
    async (dispatch) => {
        dispatch(reloadCaseDataStartAction(path));
        try {
            const { th } = await dispatch(readThunk({ path }));
            const result = await getConfig({ th, path: parentNodepath, showOperdata });
            const processedData = processData(result);
            dispatch(reloadCaseDataCompleteAction(parentNodepath, processedData));
            dispatch(setCaseCompleteAction(path, caseValue));
        } catch (error) {
            dispatch(reloadCaseDataErrorAction(path, error));
        }
    }
);

export {
    getActionPath as _getActionPath,
    getParentPaths as _getParentPaths,
    processData as _processData,
    processSchemas as _processSchemas,
    reduceSchemas as _reduceSchemas,
    getDataAndSchemas as _getDataAndSchemas,
};
