import { isFunction } from './functional';
import { isPromise } from './promise';

export const delay = milliseconds => data =>
    new Promise(resolve => setTimeout(() => resolve(data), milliseconds));

export const debounce = (
    target,
    {
        milliseconds = 100,
        leading = false,
        cleanup,
        // wait until "milliseconds" after the last call to the debouncer
        // before invoking the target function
        waitWhileSpammed = false,
    } = {},
) => {
    let timeout;
    let timestamp;
    let functionArguments;

    const later = (force) => {
        const remaining = Date.now() - timestamp;
        if (!force &&
            waitWhileSpammed &&
            remaining < milliseconds &&
            remaining >= 0
        ) {
            timeout = setTimeout(later, milliseconds - remaining);
            return;
        }
        if (force || !leading) {
            target(...functionArguments);
        }
        functionArguments = undefined;
        timeout = undefined;
        if (isFunction(cleanup)) {
            cleanup();
        }
    };

    const debounced = (...args) => {
        timestamp = Date.now();
        functionArguments = args;
        if (leading && !timeout) {
            target(...functionArguments);
        }
        if (!timeout) {
            timeout = setTimeout(later, milliseconds);
        }
    };

    debounced.cancel = () => {
        clearTimeout(timeout);
        functionArguments = undefined;
        timeout = undefined;
        if (isFunction(cleanup)) {
            cleanup();
        }
    };

    debounced.flush = () => {
        if (timeout) {
            clearTimeout(timeout);
            later(true);
        }
    };

    debounced.isWaiting = () => !!timeout;

    return debounced;
};

export const debounceWithDispatch = (
    target,
    {
        milliseconds = 100,
        leading = false,
        cleanup,
        // wait until "milliseconds" after the last call to the debouncer
        // before invoking the target function
        waitWhileSpammed = false,
    } = {},
) => {
    let dispatcher;

    const dispatchFunction = (...args) => {
        dispatcher(target(...args));
    };

    const dispatchedCleanup = () => {
        dispatcher = undefined;
        if (isFunction(cleanup)) {
            cleanup();
        }
    };

    const debounced = debounce(
        dispatchFunction,
        {
            milliseconds,
            leading,
            cleanup: dispatchedCleanup,
            waitWhileSpammed,
        },
    );

    const wrapped = (...args) => (dispatch) => {
        dispatcher = dispatch;
        debounced(...args);
    };

    Object.assign(wrapped, debounced);

    return wrapped;
};

const debounceFunctionTarget = (func, ...args) => {
    if (isFunction(func)) {
        func(...args);
    }
};

export const debounceFunction = (
    {
        target = debounceFunctionTarget,
        milliseconds = 100,
        leading = false,
        cleanup,
        // wait until "milliseconds" after the last call to the debouncer
        // before invoking the target function
        waitWhileSpammed = false,
    } = {},
) => debounce(target, {
    milliseconds,
    leading,
    cleanup,
    waitWhileSpammed,
});

export const takeLastFactory = () => {
    let previousRequestPromise = Promise.resolve();
    let lastUpdateFun;
    return async (currentAsyncUpdateFun) => {
        lastUpdateFun = currentAsyncUpdateFun;
        try {
            await previousRequestPromise;
        } catch (e) {
            // Ignore rejected promise
        }
        if (lastUpdateFun === currentAsyncUpdateFun) {
            previousRequestPromise = currentAsyncUpdateFun();
        }
    };
};

class AbortError extends Error {
    constructor() {
        super();
        this.message = 'Asynchronous function was aborted';
        this.name = 'AbortError';
    }
}

export const abortableAsync = ({ AbortCtrl = AbortController } = {}) => {
    const ctrl = new AbortCtrl();
    const signal = ctrl.signal;
    const aborted = () => !!(signal && signal.aborted);
    const abort = () => { ctrl.abort(); };
    return {
        check: () => {
            if (aborted()) {
                throw new AbortError();
            }
        },
        abort,
        aborted,
        listen: (cb) => {
            if (!aborted()) {
                signal.addEventListener('abort', cb);
                return () => {
                    signal.removeEventListener('abort', cb);
                };
            }
            return () => undefined;
        },
        signal,
    };
};

export const abortWrapper = (func, { aborter } = {}) => {
    const abortable = aborter || abortableAsync();
    const signal = abortable ? abortable.signal : undefined;
    return (...args) => {
        const ret = func(abortable, signal, ...args);
        if (isPromise(ret) && abortable) {
            ret.abort = abortable.abort;
        }
        return ret;
    };
};

export {
    AbortError,
    debounceFunctionTarget as _debounceFunctionTarget,
};
