export const getLeafrefValuesThunk = ({
    createPath,
    limit,
    path,
    startsWith,
    funcs: {
        getLeafrefValues = _getLeafrefValues,
        readThunk = read,
    } = {},
} = {},
) => (
    async (dispatch) => {
        try {
            dispatch(getLeafrefValuesStartAction(path));
            const { th } = await dispatch(readThunk({ path }));
            const res = await getLeafrefValues({
                limit,
                path: createPath || path,
                skipGrouping: true,
                startsWith,
                th,
            });
            dispatch(getLeafrefValuesCompleteAction(path, res.values, res.source));
        } catch (error) {
            dispatch(getLeafrefValuesErrorAction(path, error));
        }
    }
);

export const query = ({
    selection,
    path,
    sort,
    sortOrder,
    initialOffset,
    chunkSize,
    result_as = 'leaf_value_as_string', // eslint-disable-line camelcase
    xpathExpr,
    contextNode,
    funcs: {
        jsonrpc = jsonrpcSimple,
        readThunk = read,
    } = {},
} = {}) => (dispatch) => { // eslint-disable-line sonarjs/cognitive-complexity
    dispatch(queryPendingAction(path));
    return dispatch(readThunk({ path })).then(({ th }) => {
        const params = {
            th,
            path,
            selection,
            result_as,
        };
        if (sort !== undefined) params.sort = Array.isArray(sort) ? sort : [sort];
        if (sortOrder !== undefined) params.sort_order = sortOrder;
        if (xpathExpr !== undefined) params.xpath_expr = xpathExpr;
        if (contextNode !== undefined) params.context_node = contextNode;
        if (initialOffset || chunkSize) {
            params.initial_offset = initialOffset || 1;
            params.chunk_size = chunkSize || 10;
        }

        return jsonrpc('query', params).then(result =>
            dispatch(queryAction(
                result,
                selection,
                path,
                sort,
                sortOrder,
                chunkSize,
            )),
        );
    }).catch(error =>
        dispatch(queryErrorAction(error, selection, path)),
    );
};
