import { omitKeys } from '@webui-one/core/utils/object';
import * as nodesActionTypes from 'modules/nodes/actionTypes';
import * as userdataActionTypes from 'modules/userdata/actionTypes';
import * as systemSettingActionTypes from 'modules/systemSetting/actionTypes';
import * as yangActionTypes from 'modules/yangActions/actionTypes';
import { selectColumnsVisibility } from 'modules/userdata/selectors';
import { userMetadataPath, userProfileDataPath, userProfilePath, METADATA_TAG } from 'modules/userdata/paths';

export const reduceColumn = (state, action) => {
    const columnsVisibility = selectColumnsVisibility({ userdata: state });
    const { key, path, value } = action;
    return { [path]: { ...columnsVisibility[path], [key]: value } };
};

const valueActions = [
    nodesActionTypes.GET_VALUE,
    nodesActionTypes.SET_VALUE_COMPLETE,
];

const shouldUpdateData = (state, action) => {
    const { path } = action;
    const user = state.user;
    if (!user) {
        return false;
    }
    const metaPath = userMetadataPath(user);
    const profileDataPath = userProfileDataPath(user, METADATA_TAG);
    const profilePath = userProfilePath(user);
    const deletePaths = [metaPath, profileDataPath, profilePath];
    const actions = [
        { actionTypes: [userdataActionTypes.SET_USERDATA], paths: [metaPath, undefined] },
        { actionTypes: valueActions, paths: [metaPath] },
        { actionTypes: [nodesActionTypes.DELETE], paths: deletePaths },
    ];
    return !!actions.find(({ actionTypes, paths }) => (
        actionTypes.includes(action.type) && paths.includes(path)
    ));
};

const updateData = (state, action) => {
    if (!shouldUpdateData(state, action)) {
        return state;
    }
    if (valueActions.includes(action.type)) {
        try {
            return {
                ...state,
                data: JSON.parse(action.value),
            };
        } catch (e) {
            return {
                ...state,
                data: {},
            };
        }
    }
    if (action.type === nodesActionTypes.DELETE) {
        return omitKeys(state, ['data']);
    }
    return {
        ...state,
        data: { ...action.data },
    };
};

const initialState = {};

export default function userdata(state = initialState, action = {}) {
    switch (action.type) {
    case userdataActionTypes.SET_USERDATA:
        return updateData(state, action);
    case systemSettingActionTypes.GET_SYSTEM_SETTING:
        return {
            ...state,
            user: action.systemSetting.user,
        };
    case nodesActionTypes.GET_VALUE: {
        return updateData(state, action);
    }
    case nodesActionTypes.SET_VALUE_COMPLETE: {
        return updateData(state, action);
    }
    case nodesActionTypes.DELETE: {
        return updateData(state, action);
    }
    case nodesActionTypes.COLUMN_VISIBILITY: {
        const columnsVisibility = state.data ? state.data.columnsVisibility : undefined;
        return {
            ...state,
            data: {
                ...state.data,
                columnsVisibility: {
                    ...columnsVisibility,
                    ...reduceColumn(state, action),
                },
            },
        };
    }
    case yangActionTypes.KEEP_INPUT_PARAMS: {
        const data = state.data ? state.data : {};
        const keepInputParams = data.keepInputParams ? data.keepInputParams : {};
        return {
            ...state,
            data: {
                ...data,
                keepInputParams: {
                    ...keepInputParams,
                    [action.actionPath]: !keepInputParams[action.actionPath],
                },
            },
        };
    }
    default:
        return state;
    }
}
