import { formatPath } from '@webui-one/core/utils/path';
import { ALARM_MANAGER_NAME } from './defines';
import { getLinkWithDevice } from './utils';

export default {
    path: () => formatPath(`${ALARM_MANAGER_NAME}*`),
    getComponent(nextState, cb) {
        import(/* webpackChunkName: "AlarmManager" */'./AlarmManager').then((AlarmManager) => {
            cb(null, AlarmManager.default);
        }, () => undefined)
            .catch(() => undefined);
    },
};

export {
    getLinkWithDevice,
    ALARM_MANAGER_NAME,
};
