/* eslint-disable import/prefer-default-export */
import { formatPath } from '@webui-one/core/utils/path';
import { ALARM_MANAGER_NAME } from './defines';
import { lstrip } from '@webui-one/core/utils/string';

export const getLinkWithDevice = (device) => {
    if (device) {
        return formatPath(`${ALARM_MANAGER_NAME}/device/${device}`);
    }
    return formatPath(`${ALARM_MANAGER_NAME}`);
};

export const getDeviceFromUrl = url => lstrip(url, '/device/');
