import { last } from '@webui-one/core/utils/array';
import { invariant } from '@webui-one/core/utils/functional';
import {
    compilePattern,
} from './match';


export function findMatchingEndParenIndex(
    tokens,
    startIndex = 0,
    startLevel = 0,
) {
    let index = -1;
    let level = startLevel;
    const endLevel = startLevel > 0 ? startLevel - 1 : 0;
    for (let i = startIndex; i < tokens.length; i += 1) {
        const token = tokens[i];
        if (token === '(') {
            level += 1;
        }
        if (token === ')') {
            level -= 1;
            if (level === endLevel) {
                index = i;
                break;
            }
        }
    }
    return index;
}

// eslint-disable-next-line sonarjs/cognitive-complexity
export function formatPattern(pattern, params) {
    params = params || {}; // eslint-disable-line no-param-reassign

    const { tokens } = compilePattern(pattern);
    const pathname = [];
    const parenHistory = [];
    let splatIndex = 0;
    let currentParen;

    let token;
    let paramName;
    let paramValue;
    for (let i = 0, len = tokens.length; i < len; i += 1) {
        token = tokens[i];

        if (token === '*' || token === '**') {
            paramValue = Array.isArray(params.splat)
                ? params.splat[splatIndex++] // eslint-disable-line no-plusplus
                : params.splat;

            invariant(
                paramValue != null || parenHistory.length,
                `Missing splat #${splatIndex} for path "${pattern}"`,
            );

            if (paramValue != null) {
                pathname.push(encodeURI(paramValue));
            }
        } else if (token === '(') {
            currentParen = [];
            parenHistory.push(currentParen);
        } else if (token === ')') {
            const parenText = parenHistory.pop().join('');
            currentParen = last(parenHistory);

            if (currentParen) {
                currentParen.push(parenText);
            } else {
                pathname.push(parenText);
            }
        } else if (token === '\\(') {
            pathname.push('(');
        } else if (token === '\\)') {
            pathname.push(')');
        } else if (token.charAt(0) === ':') {
            paramName = token.substring(1);
            paramValue = params[paramName];

            invariant(
                paramValue != null || parenHistory.length > 0,
                `Missing "${paramName}" parameter for path "${pattern}"`,
            );

            if (paramValue == null) {
                if (parenHistory.length > 0) {
                    currentParen.length = 0;

                    const tokenIdx = tokens.indexOf(token);
                    const segment = tokens.slice(
                        tokenIdx,
                        tokens.length,
                    ).join('');
                    const nextParenIdx = findMatchingEndParenIndex(
                        tokens,
                        tokenIdx + 1,
                        parenHistory.length,
                    );

                    invariant(
                        nextParenIdx !== -1,
                        `Path "${pattern}" is missing end paren at segment "${segment}"`,
                    );

                    i = nextParenIdx - 1;
                }
            } else if (parenHistory.length) {
                currentParen.push(encodeURIComponent(paramValue));
            } else {
                pathname.push(encodeURIComponent(paramValue));
            }
        } else if (parenHistory.length) {
            currentParen.push(token);
        } else {
            pathname.push(token);
        }
    }

    invariant(
        parenHistory.length === 0,
        `Path "${pattern}" is missing end paren`,
    );

    return pathname.join('').replace(/\/+/g, '/');
}
