import { connect } from 'react-redux';
import { getSystemSetting } from 'modules/systemSetting/actions';

import {
    selectAuthenticated,
} from 'modules/application/selectors';
import {
    setAuthenticated,
} from 'modules/application/actions';
import Comet from '@webui-one/components/Comet/Comet';
import {
    handleFatalError as handleFatalErrorFunc,
} from '@webui-one/core/utils/session';
import {
    upgradeCommitAction,
    upgradeCompleteAction,
    upgradeInitAction,
} from 'modules/transactions/actions';
import {
    subscribeMessageHandlerAction,
    subscribeUpgradeHandlerAction,
    unsubscribeMessageHandlerAction,
    unsubscribeUpgradeHandlerAction,
} from 'modules/comet/actions';
import {
    selectCdbOperHandlers,
    selectChangesHandlers,
    selectMessageHandlers,
    selectUpgradeHandlers,
} from 'modules/comet/selectors';
import { getModelsAndPackages } from 'ConfigurationEditor/utils';

const emptyArray = Object.freeze([]);

const mapStateToProps = state => (
    {
        cdboperHandlers: selectCdbOperHandlers(state) || emptyArray,
        changesHandlers: selectChangesHandlers(state) || emptyArray,
        isAuthenticated: selectAuthenticated(state),
        messageHandlers: selectMessageHandlers(state) || emptyArray,
        upgradeHandlers: selectUpgradeHandlers(state) || emptyArray,
    }
);

const mapDispatchToProps = dispatch => ({
    onSubscribeMessageHandler(handler) {
        dispatch(subscribeMessageHandlerAction(handler));
    },
    onUnsubscribeMessageHandler(handler) {
        dispatch(unsubscribeMessageHandlerAction(handler));
    },
    onSubscribeUpgradeHandler(handler) {
        dispatch(subscribeUpgradeHandlerAction(handler));
    },
    onFatalError(
        error,
        isAuthenticated,
        { funcs: { handleFatalError = handleFatalErrorFunc } = {} } = {},
    ) {
        return handleFatalError(
            error,
            isAuthenticated,
            {
                dispatch,
                setAuthenticated,
            },
        );
    },
    onUnsubscribeUpgradeHandler(handler) {
        dispatch(unsubscribeUpgradeHandlerAction(handler));
    },
    onUpgradeCommit() {
        dispatch(upgradeCommitAction());
    },
    onUpgradeInit() {
        dispatch(upgradeInitAction());
    },
    onUpgradeComplete() {
        return dispatch(getSystemSetting('all', true)).then(() => {
            dispatch(upgradeCompleteAction());
            return dispatch(getModelsAndPackages());
        });
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(Comet);

export {
    mapDispatchToProps as _mapDispatchToProps,
};
