import { omitKeys } from '@webui-one/core/utils/object';
import * as actionTypes from './actionTypes';

const initialState = {
};

export default function reducer(state = initialState, action = {}) {
    switch (action.type) {
    case actionTypes.RUN_ACTION_START:
        return {
            ...state,
            [action.actionPath]: {
                action: action.actionPath.split('/').pop(),
                resultPending: true,
                startedAt: action.startedAt,
                result: undefined,
                error: undefined,
                status: 'pending',
            },
        };
    case actionTypes.RUN_ACTION_COMPLETE:
    case actionTypes.RUN_CUSTOM_ACTION_COMPLETE:
        return { ...state,
            [action.actionPath]: {
                ...state[action.actionPath],
                resultPending: false,
                result: action.result,
                error: undefined,
                completedAt: action.completedAt,
                status: 'completed',
            },
        };
    case actionTypes.RUN_ACTION_ERROR:
        return {
            ...state,
            [action.actionPath]: {
                ...state[action.actionPath],
                resultPending: false,
                error: action.error,
                result: undefined,
                completedAt: action.completedAt,
                status: 'error',
            },
        };
    case actionTypes.CLEAR_ACTION_OUTPUT:
        return {
            ...state,
            [action.actionPath]: {
                ...state[action.actionPath],
                error: undefined,
                result: undefined,
                completedAt: undefined,
            },
        };
    case actionTypes.CLEAR_ACTION:
        return omitKeys(state, [action.actionPath]);
    default:
        return state;
    }
}
