import { formatPath } from '@webui-one/core/utils/path';

import {
    SERVICE_MANAGER_NAME,
} from './defines';

import {
    getLinkWithPath,
} from './utils';

export default {
    path: () => formatPath(`${SERVICE_MANAGER_NAME}*`),
    getComponent(nextState, cb) {
        import(/* webpackChunkName: "ServiceManagerContainer" */'./ServiceManagerContainer').then((ServiceManagerContainer) => {
            cb(null, ServiceManagerContainer.default);
        }, () => undefined)
            .catch(() => undefined);
    },
};

export {
    getLinkWithPath,
    SERVICE_MANAGER_NAME,
};
