/* eslint-disable import/prefer-default-export */
import { formatPath } from '@webui-one/core/utils/path';
import {
    getModelsThunk,
} from 'modules/models/actions';
import {
    getTransChangesAsyncAction,
} from 'modules/transactions/actions';
import {
    getPackagesAsyncAction,
} from 'modules/packages/actions';
import { selectUpgradePending } from 'modules/transactions/selectors';
import { getModelsAndPackagesAction } from './actions';
import { selectIsLoading } from './reducer';
import {
    CONFIGURATION_EDITOR_NAME,
} from './defines';

export const skipGettingModelsAndPackages = state => (
    selectUpgradePending(state)
    || selectIsLoading(state)
);

export const getModelsAndPackages = ({
    funcs: {
        getModels = getModelsThunk,
        skipModelsAndPackages = skipGettingModelsAndPackages,
    } = {},
} = {}) => (
    (dispatch, getState) => {
        // NOTE(dbildstr) skip if we are reloading packages or already loading
        // packages and/or models
        if (skipModelsAndPackages(getState())) {
            return Promise.resolve(true);
        }
        dispatch(getModelsAndPackagesAction());
        dispatch(getPackagesAsyncAction());
        return dispatch(getModels())
            .then(() => dispatch(getTransChangesAsyncAction()));
    }
);

export const getLinkWithPath = path => formatPath(CONFIGURATION_EDITOR_NAME + path);
