import { createSelector } from 'reselect';
import {
    quoteKey,
} from '@webui-one/core/utils/key';
import {
    isString,
} from '@webui-one/core/utils/string';
import {
    findCommonAncestor,
    pathWithoutKeys,
    pathToObjects,
} from '@webui-one/core/keypath/kp-parser';
import {
    pathIsDeleted,
    pathIsDirty,
    selectChanges,
    selectDirtyPaths,
} from 'modules/transactions/selectors';
import { isEmptyArray } from '@webui-one/core/utils/array';


export const selectNodes = state => state.nodes;

export const selectNodeReselect = createSelector(
    selectNodes,
    (_, ownProps) => ownProps.path,
    (stateNode, path) => {
        if (!(path in stateNode)) {
            return {};
        }
        return stateNode[path];
    },
);

const emptyNode = Object.freeze({});
export function selectNode(state, path) {
    if (!(path in state.nodes)) {
        return emptyNode;
    }

    return state.nodes[path];
}

export const selectTotalNumberOfResults = (state, path) =>
    selectNode(state, path).totalNumberOfResults || 0;

export const selectRows = (state, path) => {
    const rows = selectNode(state, path).rows;
    return Array.isArray(rows) ? rows : [];
};

export const selectError = (state, path) => selectNode(state, path).error;
export const selectsortOrder = (state, path) => selectNode(state, path).sortOrder;
export const selectSortColumn = (state, path) => selectNode(state, path).sort;
export const selectOffset = (state, path) => selectNode(state, path).offset;
export const selectHadDataReselect = createSelector(
    selectNodeReselect,
    node => node.hadData || {},
);

export const selectNodeRows = createSelector(
    selectNodeReselect,
    node => node.rows,
);

export const getRowKeypath = (basepath, keynames = [], values = {}, ix = 0) => {
    const identifiers = keynames.reduce((acc, cur) => {
        const value = values[cur];
        if (typeof value === 'string') {
            acc.push(quoteKey(value));
        }
        return acc;
    }, []);

    if (identifiers.length === 0) return `${basepath}{${ix + 1}}`;

    return `${basepath}{${identifiers.join(' ')}}`;
};

export const getLeafListRowKeypath = (basepath, values) => `${basepath}{${quoteKey(values[0])}}`;

// if keypath values extract value
export const getValues = row =>
    Object.entries(row)
        .reduce((acc, [k, v]) => ({
            ...acc,
            [k]: v !== undefined && v.value !== undefined ? v.value : v,
        }), {});

export const createNodeRowSelector = (
    {
        funcs: {
            getDirtyPaths = selectDirtyPaths,
        } = {},
    } = {},
) => createSelector(
    (_, ownProps) => ownProps.path,
    (_, ownProps) => ownProps.isLeafList,
    (_, ownProps) => ownProps.keyNames,
    (_, ownProps) => ownProps.chunkSize,
    selectNodeRows,
    selectNodes,
    getDirtyPaths,
    (path, isLeafList, keyNames, chunkSize, rows = [], stateNodes = undefined, dirtyPaths = []) => (
        rows.map(getValues).reduce((acc, row, ix) => {
            if (ix > (chunkSize - 1)) return acc;

            const rowKeypath = isLeafList ?
                getLeafListRowKeypath(path, row) :
                getRowKeypath(path, keyNames, row, ix);

            const isDeleted = pathIsDeleted(rowKeypath, dirtyPaths);

            if (isDeleted) {
                return acc;
            }

            const isDirty = pathIsDirty(rowKeypath, dirtyPaths);

            const isSelected =
                stateNodes &&
                stateNodes[path] &&
                stateNodes[path].selectedRows &&
                stateNodes[path].selectedRows[rowKeypath];

            acc.push({
                rowKey: rowKeypath,
                data: { ...row, isDirty, isSelected },
            });

            return acc;
        }, [])
    ),
);

const mergeChangesAndRows = (
    changes = [],
    rows = [],
    selectedRows = {},
    path = undefined,
    keyNames = [],
) => {
    if (isEmptyArray(changes)) return rows;
    if (isEmptyArray(keyNames)) return rows;
    return changes.reduce((acc, change) => {
        // verify change has correct properties. 'op' and a valid 'path'
        if (change.op !== 'created') return acc;
        if (!isString(change.path)) return acc;
        if (!change.path.startsWith(path)) return acc;
        if (change.path[0] !== '/') return acc;

        // If the change already exists in rows, dont't merge it
        const found = rows.find(row => (
            row.rowKey === change.path
        ));
        if (found !== undefined) return acc;

        const objs = pathToObjects(change.path);
        const lastObj = objs[objs.length - 1];
        // If the last obj from pathToObjects is not the parentPath of the current list
        // this change entry is for some other list on the same page.
        if (lastObj.parentPath !== path) return acc;

        const keyValues = keyNames.reduce((accKeys, k, i) => ({
            ...accKeys,
            [k]: lastObj.keys[i],
        }), {});

        acc.push({
            rowKey: change.path,
            isDeleted: false,
            data: {
                isDirty: true,
                isSelected: !!selectedRows[change.path],
                ...keyValues,
            },
        });

        return acc;
    }, [...rows]);
};

export const selectSelectedRows = (state, path) => selectNode(state, path).selectedRows;
export const createNodeRowAndChangesSelector = () => createSelector(
    createNodeRowSelector(),
    selectChanges,
    (state, ownProps) => (selectSelectedRows(state, ownProps.path)),
    (_, ownProps) => ownProps.path,
    (_, ownProps) => ownProps.keyNames,
    (rows, changes, selectedRows, path, keyNames) => (
        mergeChangesAndRows(changes, rows, selectedRows, path, keyNames)
    ),
);

export const selectExists = (state, path) => selectNode(state, path).exists;

export const selectCaseValue = (state, path) => selectNode(state, path).caseValue;

export const selectDepNode = (state, dep) => selectNodes(state)[dep];

export const selectDepNodes = (state, path, deps) => {
    let leafDepsNodes = [];
    if (deps && Array.isArray(deps)) {
        leafDepsNodes = deps.reduce((acc, dep) => {
            let depPath = dep;

            const commonAncestor = findCommonAncestor(path, dep);
            if (commonAncestor) {
                depPath = dep.replace(pathWithoutKeys(commonAncestor), commonAncestor);
            }
            if (depPath === path) return acc;

            const depNode = selectDepNode(state, depPath);
            acc.push(depNode);
            return acc;
        }, []);
    }
    return leafDepsNodes;
};

export const selectLoading = (state, path) => selectNode(state, path).isLoading !== false;
export const selectRawLoading = (state, path) => selectNode(state, path).isLoading;

export const selectDirty = (
    state,
    path,
    {
        funcs: {
            getDirtyPaths = selectDirtyPaths,
        } = {},
    } = {},
) => {
    const dirtyPaths = getDirtyPaths(state, { path });
    return pathIsDirty(path, dirtyPaths);
};

export {
    mergeChangesAndRows as _mergeChangesAndRows,
    getRowKeypath as _getRowKeypath,
    getLeafListRowKeypath as _getLeafListRowKeypath,
};
