import * as actionTypes from './actionTypes';
import {
    formatAppHref,
} from './actions';
import * as nodesActionTypes from 'modules/nodes/actionTypes';
import defaultApplications from '@webui-one/core/defaultApplications';
import { omitKeys } from '@webui-one/core/utils/object';
import { pathToObjects } from '@webui-one/core/keypath/kp-parser';

const initialState = {
    applications: defaultApplications,
};

const appTemplate = {
    id: '',
    category: '',
    href: '',
    title: '',
    description: '',
    'promo-label': '',
    abbreviation: '',
    shortcut: '',
    hidden: '',
};

const appPath = '/webui:webui/webui-one:applications/application';

function handleCreate(state, action) {
    const obj = pathToObjects(action.path);
    const listItem = obj.pop();
    if (!listItem || listItem.parentPath !== appPath) {
        return state;
    }
    const id = listItem.name;
    const href = action.data && action.data.href
        ? formatAppHref(action.data.href)
        : '';
    return {
        ...state,
        applications: {
            ...state.applications,
            [id]: {
                ...appTemplate,
                ...action.data,
                id,
                href,
            },
        },
    };
}
function handleDelete(state, action) {
    const paths = Array.isArray(action.paths) ? action.paths : [action.path];

    return paths.reduce((acc, path) => {
        const obj = pathToObjects(path);
        const node = obj.pop();
        if (action.path === appPath) {
            acc.applications = {
                ...defaultApplications,
            };
            return acc;
        }
        if (node.type === 'key' && node.parentPath === appPath) {
            acc.applications = {
                ...defaultApplications,
                ...omitKeys(state.applications, [node.name]),
            };
            return acc;
        }
        const listItem = obj.pop();
        if (!listItem || listItem.parentPath !== appPath) {
            return acc;
        }
        const id = listItem.name;
        const application = state.applications ? state.applications[id] : {};
        acc.applications = {
            ...state.applications,
            [id]: {
                ...omitKeys(application, [node.name]),
            },
        };
        return acc;
    }, { ...state });
}

function handleSetValue(state, action) {
    const obj = pathToObjects(action.path);
    const leaf = obj.pop();
    const listItem = obj.pop();
    if (!listItem || listItem.parentPath !== appPath) {
        return state;
    }
    const leafName = leaf.name;
    const id = listItem.name;
    const application = state.applications ? state.applications[id] : {};
    const value = leafName === 'href'
        ? formatAppHref(action.value)
        : action.value;
    return {
        ...state,
        applications: {
            ...state.applications,
            [id]: {
                ...application,
                [leafName]: value,
            },
        },
    };
}

export default function reducer(state = initialState, action = {}) {
    switch (action.type) {
    case actionTypes.APPLICATIONS_GET_START:
        return {
            ...state,
            isLoading: true,
        };
    case actionTypes.APPLICATIONS_GET:
        return {
            ...state,
            isLoading: false,
            applications: {
                ...defaultApplications,
                ...action.applications,
            },
        };
    case nodesActionTypes.CREATE:
        return handleCreate(state, action);
    case nodesActionTypes.DELETE:
    case nodesActionTypes.DELETE_PATHS:
        return handleDelete(state, action);
    case nodesActionTypes.SET_VALUE_COMPLETE:
        return handleSetValue(state, action);
    default:
        return state;
    }
}
