import React from 'react';
import PropTypes from 'prop-types';

import { isFunction } from '@webui-one/core/utils/functional';
import { isNonEmptyArray } from '@webui-one/core/utils/array';

class ShortcutComponent extends React.Component {
    constructor(props) {
        super(props);
        this.handleKeyPress = this.handleKeyPress.bind(this);
        this.keyMatches = '';
    }

    componentDidMount() {
        document.addEventListener('keypress', this.handleKeyPress, false);
    }

    componentWillUnmount() {
        document.removeEventListener('keypress', this.handleKeyPress);
    }

    handleKeyPress(e) {
        const { shortcuts } = this.props;
        const matchString = this.keyMatches + e.key;
        if (e.target !== document.body &&
            e.target.nodeName !== 'BUTTON' &&
            e.target.nodeName !== 'A') {
            this.keyMatches = '';
            return;
        }
        let match = '';
        let firstmatch = '';
        let foundMatch = false;
        const abbreviations = Object.keys(shortcuts);
        abbreviations.forEach((abbreviation) => {
            const handlers = shortcuts[abbreviation];
            if (abbreviation === matchString && isNonEmptyArray(handlers)) {
                handlers
                    .filter(handler => isFunction(handler))
                    .forEach(handler => handler(matchString));
                this.keyMatches = '';
                foundMatch = true;
                return;
            }
            if (abbreviation.startsWith(matchString)) {
                match = matchString;
            } else if (abbreviation.startsWith(e.key)) {
                firstmatch = e.key;
            }
        });
        if (foundMatch) this.keyMatches = '';
        else this.keyMatches = (match === '' ? firstmatch : match);
    }

    render() {
        return null;
    }
}

ShortcutComponent.propTypes = {
    shortcuts: PropTypes.shape({}),
};

ShortcutComponent.defaultProps = {
    shortcuts: {},
};

export default ShortcutComponent;
