/* eslint-disable import/prefer-default-export */
import { getApplicationKey } from '@webui-one/core/utils/path';
import {
    commitCompleteAction,
    commitErrorAction,
    commitStartAction,
    commitDryRunCompleteAction,
    commitDryRunErrorAction,
    commitDryRunStartAction,
    formattedCommitDryRunCompleteAction,
    formattedCommitDryRunErrorAction,
    formattedCommitDryRunStartAction,
    nativeCommitDryRunCompleteAction,
    nativeCommitDryRunErrorAction,
    nativeCommitDryRunStartAction,
    deleteTransAction,
    getTrans as getTransThunk,
    getWriteTrans as getWriteTransThunk,
    reevaluate as reevaluateThunk,
    tryGetTransChanges as tryGetTransChangesThunk,
} from 'modules/transactions/actions';
import {
    selectCommitDryRunInProgress,
    selectCommitInProgress,
    selectNativeCommitDryRunInProgress,
} from 'modules/transactions/selectors';
import {
    setUserdataAction,
} from 'modules/userdata/actions';

import {
    waiter as waiterFunc,
} from '@webui-one/core/utils/time';
import * as ipcActionTypes from './actionTypes';

export const ipcErrorAction = error => ({
    type: ipcActionTypes.IPC_ERROR,
    error,
});

export const ipcModificationAction = ({ path }) => ({
    type: ipcActionTypes.IPC_MODIFICATION,
    path,
});

export const ipcDeleteTrans = ({ th, changes, dataPaths }) =>
    dispatch => (
        dispatch(deleteTransAction(th, changes, dataPaths))
    );

export const ipcTransUpdate = ({
    path,
    funcs: {
        getTrans = getTransThunk,
        getWriteTrans = getWriteTransThunk,
        reevaluate = reevaluateThunk,
        tryGetTransChanges = tryGetTransChangesThunk,
        waiter = waiterFunc,
    } = {},
} = {}) =>
    (dispatch, getState) => {
        if (getApplicationKey(window.location.pathname) === 'CommitManager') {
            if (selectCommitDryRunInProgress(getState()) ||
                    selectCommitInProgress(getState()) ||
                    selectNativeCommitDryRunInProgress(getState())) {
                return waiter(() => dispatch(ipcTransUpdate({
                    funcs: {
                        getWriteTrans,
                        reevaluate,
                        tryGetTransChanges,
                        waiter,
                    },
                })), 100);
            }
            return dispatch(getWriteTrans()).then((res) => {
                const { th } = res;
                if (th) {
                    return dispatch(reevaluate({ th }));
                }
                return Promise.resolve();
            });
        }
        return dispatch(getTrans()).then(() => {
            dispatch(ipcModificationAction({ path }));
            return dispatch(tryGetTransChanges());
        });
    };

export const ipcCommitComplete = ({ th }) =>
    dispatch => (
        dispatch(commitCompleteAction(th))
    );

export const ipcCommitStart = ({ th }) =>
    dispatch => (
        dispatch(commitStartAction(th))
    );

export const ipcCommitError = ({ error }) =>
    dispatch => (
        dispatch(commitErrorAction(error))
    );

export const ipcCommitDryRunStart = ({ th }) =>
    dispatch => (
        dispatch(commitDryRunStartAction(th))
    );

export const ipcCommitDryRunComplete = ({ result, th }) =>
    dispatch => (
        dispatch(
            commitDryRunCompleteAction(th, {
                dry_run_result: { cli: result },
            }),
        )
    );

export const ipcCommitDryRunError = ({ error, th }) =>
    dispatch => (
        dispatch(commitDryRunErrorAction(th, error))
    );

export const ipcNativeCommitDryRunStart = ({ th }) =>
    dispatch => (
        dispatch(nativeCommitDryRunStartAction(th))
    );

export const ipcNativeCommitDryRunComplete = ({ result, th }) =>
    dispatch => (
        dispatch(
            nativeCommitDryRunCompleteAction(th, {
                dry_run_result: { native: result },
            }),
        )
    );

export const ipcNativeCommitDryRunError = ({ error, th }) =>
    dispatch => (
        dispatch(nativeCommitDryRunErrorAction(th, error))
    );

export const ipcFormattedCommitDryRunStart = ({ th }) =>
    dispatch => (
        dispatch(formattedCommitDryRunStartAction(th))
    );

export const ipcFormattedCommitDryRunComplete = ({ format, result, th }) =>
    dispatch => (
        dispatch(
            formattedCommitDryRunCompleteAction(th, {
                dry_run_result: {
                    [`result_${format}`]: result,
                },
            }),
        )
    );

export const ipcFormattedCommitDryRunError = ({ error, th }) =>
    dispatch => (
        dispatch(formattedCommitDryRunErrorAction(th, error))
    );

export const ipcSubscribeAction = (handler, actionTypes) => ({
    type: ipcActionTypes.IPC_SUBSCRIBE,
    handler,
    actionTypes,
});

export const ipcUnsubscribeAction = handler => ({
    type: ipcActionTypes.IPC_UNSUBSCRIBE,
    handler,
});

export const ipcSetUserdata = ({ data, path }) =>
    dispatch => dispatch(setUserdataAction(data, path));
