
import {
    isFunction,
} from './functional';
import {
    envHas,
} from './platform';

const defaultPredicate = () => false;

let timingPredicate = defaultPredicate;

const getPredicate = () => timingPredicate;

export const isTraceBuild = ({ hasEnv = envHas } = {}) =>
    hasEnv('WEBUI_TRACING', true);

export const tracingIsEnabled = ({
    traceBuild = isTraceBuild,
} = {}) => window.performance
    && isFunction(window.performance.mark)
    && (
        traceBuild()
        || timingPredicate()
    );

export function initTracing(predicate) {
    timingPredicate = isFunction(predicate) ? predicate : timingPredicate;
}

export function resetTracing() {
    timingPredicate = defaultPredicate;
}

export function startTracing(
    tag,
    {
        funcs: {
            isTracingEnabled = tracingIsEnabled,
        } = {},
    } = {},
) {
    if (!isTracingEnabled()) return;

    window.performance.mark(`${tag}_start`);
}

export function endTracing(
    tag,
    {
        funcs: {
            isTracingEnabled = tracingIsEnabled,
        } = {},
    } = {},
) {
    if (!isTracingEnabled()) return;

    window.performance.mark(`${tag}_end`);
    window.performance.measure(
        tag,
        `${tag}_start`,
        `${tag}_end`,
    );
}

export {
    defaultPredicate as _defaultPredicate,
    getPredicate as _getPredicate,
};
