import * as actionTypes from './actionTypes';
import { isObject } from '@webui-one/core/utils/object';

const EMPTY_OBJECT = Object.freeze({});
const EMPTY_ARRAY = Object.freeze([]);

const initialState = {
};

function handleGetSystemSetting(state, action) {
    const sysSetting = isObject(action.systemSetting) ? action.systemSetting : EMPTY_OBJECT;
    const customizations = Array.isArray(sysSetting.customizations) ?
        sysSetting.customizations : EMPTY_ARRAY;
    const models = Array.isArray(sysSetting.models) ? sysSetting.models : EMPTY_ARRAY;
    const webuiPackages = customizations.filter(name => name !== 'ncs');

    return {
        ...sysSetting,
        customizations,
        models,
        webuiPackages,
    };
}

export default function systemSetting(state = initialState, action = {}) {
    switch (action.type) {
    case actionTypes.GET_SYSTEM_SETTING:
        return handleGetSystemSetting(state, action);
    case actionTypes.GET_SYSTEM_SETTING_START:
        return {
            ...state,
            pending: true,
        };
    default:
        return state;
    }
}

export function selectRollbackEnabled(state) {
    return !!state?.systemSetting?.capabilities?.rollback;
}

export function selectNsoVersion(state) {
    return state.systemSetting.version;
}

export function selectNsoUser(state) {
    return state.systemSetting.user;
}

export const selectSystemSetting = state => state.systemSetting || EMPTY_OBJECT;

export const selectCustomizations = state =>
    selectSystemSetting(state).customizations || EMPTY_ARRAY;

export const selectWebuiPackages = state => selectSystemSetting(state).webuiPackages || EMPTY_ARRAY;

export const selectModels = state => selectSystemSetting(state).models || EMPTY_ARRAY;

export const selectModelByName = (state, name) => (
    (selectModels(state) || []).find(m => m.name === name)
);

export const selectModelPrefix = (state, mod) => {
    const model = selectModelByName(state, mod);
    return model && model.prefix ? model.prefix : undefined;
};

export const selectPending = state => selectSystemSetting(state).pending === true;
